<template>
  <div id="desginer" class="ds_workflow ds-designer" :class="flowModel">
    <!-- 按钮栏 -->
    <div class="designer-head">
      <div class="desginer_tools">
        <a class="ds-button--icon-text" @click="doSave">
          <i class="iconfont icon-baocun"></i>
          <span>保存</span>
        </a>
        <a class="ds-button--icon-text" @click="printImage()">
          <i class="iconfont icon-baocun"></i>
          <span>导出图片</span>
        </a>
        <!-- <a class="ds-button--icon-text">
          <i class="iconfont icon-baocun"></i>
          <span>发布</span>
        </a>
        <a class="ds-button--icon-text">
          <i class="iconfont icon-iframetianjia"></i>
          <span>
            版本
            <i class="iconfont icon-xia1"></i>
          </span>
          <dl class="layui-nav-child">
            <p class="label">
              <span>当前版本</span>
            </p>
            <p>
              <span>2.0.11 2020-4-10 16：20： 10</span>
            </p>
            <p class="label">
              <span>历史版本</span>
            </p>
            <p>
              <span>2.0.11 2020-4-10 16：20： 10</span>
            </p>
            <p>
              <span>2.0.11 2020-4-10 16：20： 10</span>
            </p>
            <p>
              <span>2.0.11 2020-4-10 16：20： 10</span>
            </p>
            <p>
              <span>2.0.11 2020-4-10 16：20： 10</span>
            </p>
          </dl>
        </a>
        <a class="ds-button--icon-text">
          <i class="iconfont icon-shebeikaifa1"></i>
          <span>模板库</span>
        </a>
        <a class="ds-button--icon-text">
          <i class="iconfont icon-baocun"></i>
          <span>关闭</span>
        </a> -->
        <a
          class="ds-button--icon-text"
          :class="{ selected: isDrawLine }"
          @click="drawLine"
        >
          <i class="iconfont icon-liujisuan"></i>
          <span>连线</span>
        </a>
        <a class="ds-button--icon-text">
          <i class="iconfont icon-iframetianjia"></i>
          <span>
            泳道
            <i class="iconfont icon-xia1"></i>
          </span>
          <dl class="layui-nav-child">
            <dd @click="addLane('vertical')">
              <span>泳道(垂直)</span>
            </dd>
            <dd @click="addLane('horizontal')">
              <span>泳道(水平)</span>
            </dd>
          </dl>
        </a>
        <a class="ds-button--icon-text scale">
          <div class="scale-com">
            <i
              class="el-icon-minus"
              :class="{ disabled: scale <= 0.4 }"
              @click="rootScale('down')"
            ></i>
            <span class="percent">{{ Math.round(scale * 100) }}%</span>
            <i
              class="el-icon-plus"
              :class="{ disabled: scale >= 2 }"
              @click="rootScale('up')"
            ></i>
          </div>
          <span>缩放</span>
        </a>
        <!-- <a class="ds-button--icon-text">
          <i class="iconfont icon-dakai"></i>
          <span>
            组件
            <i class="iconfont icon-xia1"></i>
          </span>
          <dl class="layui-nav-child">
            <p @click="exportFlow()">
              <span>创建组</span>
            </p>
            <p class="label" @click="saveAsFlow()">
              <span>保存为组件</span>
            </p>
            <p @click="openFlow()">
              <span>选择组</span>
            </p>
          </dl>
        </a> -->
        <a class="ds-button--icon-text">
          <i class="iconfont icon-lianjieliu"></i>
          <span>
            更多
            <i class="iconfont icon-xia1"></i>
          </span>
          <dl class="layui-nav-child">
            <p class="title">
              <span>导出</span>
            </p>
            <!-- <p class="child">DFR</p>
            <p class="child">BPMN2.0</p> -->
            <!-- <p class="child" @click="exportXML()">XML</p> -->
            <p class="label child" @click="exportFlowJson()">JSON</p>
            <p class="title">
              <span>导入</span>
            </p>
            <!-- <p class="child">DFR</p>
            <p class="child">BPMN2.0</p>
            <p class="child">XML</p> -->
            <p class="label child">
              <el-upload
                action=""
                accept=".json"
                :show-file-list="false"
                :on-change="uploadJson"
                :auto-upload="false">
                <span slot="trigger">JSON</span>
              </el-upload>
            </p>
            <!-- <p class="label" @click="startTestFlow()">
              <span>仿真测试</span>
            </p>
            <p @click="monitorFlow()">
              <span>浏览监控</span>
            </p> -->
          </dl>
        </a>
        <a
          v-if="selectedList && selectedList.length > 1"
          class="ds-button--icon-text"
        >
          <i class="iconfont icon-lianjieliu"></i>
          <span>
            对齐方式
            <i class="iconfont icon-xia1"></i>
          </span>
          <dl class="layui-nav-child">
            <dd @click="alignment('left')">
              <span>左对齐(L)</span>
            </dd>
            <dd @click="alignment('center')">
              <span>水平居中(C)</span>
            </dd>
            <dd @click="alignment('right')">
              <span>右对齐(R)</span>
            </dd>
            <dd @click="alignment('top')">
              <span>顶端对齐(T)</span>
            </dd>
            <dd @click="alignment('middle')">
              <span>垂直居中(M)</span>
            </dd>
            <dd @click="alignment('bottom')">
              <span>底端对齐(B)</span>
            </dd>
            <dd @click="alignment('horizontal')">
              <span>横向分布(H)</span>
            </dd>
            <dd @click="alignment('vertical')">
              <span>纵向分布(V)</span>
            </dd>
          </dl>
        </a>
        <a class="ds-button--icon-text">
          <i class="iconfont icon-iframetianjia"></i>
          <span>
            {{ flowModel === "normal" ? "标准模式" : "体系化模式" }}
            <i class="iconfont icon-xia1"></i>
          </span>
          <dl class="layui-nav-child">
            <dd @click="setFlowModel('normal')">
              <span>标准模式</span>
            </dd>
            <dd @click="setFlowModel('systematic')">
              <span>体系化模式</span>
            </dd>
          </dl>
        </a>
      </div>
    </div>
    <!-- 中、左、右 -->
    <div class="designer-content">
      <div class="designer-center">
        <div ref="ds_page" class="ds_page design" @scroll="onScroll($event)">
          <div
            ref="container"
            class="flow"
            :class="{ 'is-print-image': isPrintImage }"
            :style="{
              width: toPx(scale > 1 ? width * scale : width / scale),
              height: toPx(scale > 1 ? height * scale : height / scale),
              transform: `scale(${scale}) translateZ(0)`,
              transformOrigin: 'left top',
            }"
          >
            <div
              ref="root"
              :class="{
                has_v_head: lanes && lanes.vertical.length > 0,
                has_h_head: lanes && lanes.horizontal.length > 0,
              }"
              :style="{
                width: toPx(scale > 1 ? width * scale : width / scale),
                height: toPx(scale > 1 ? height * scale : height / scale),
              }"
              class="root"
            >
              <span
                v-if="baseLine.showV"
                :style="{ transform: `translate3d(${baseLine.left}px, 0, 0)` }"
                class="baseLine_v"
              ></span>
              <span
                v-if="baseLine.showH"
                :style="{ transform: `translate3d(0, ${baseLine.top}px, 0)` }"
                class="baseLine_h"
              ></span>
              <flow-content
                ref="flowContent"
                :is-draw-line="isDrawLine"
                :lines="lines"
                :nodes="nodes"
                :events="events"
                :checked="checked"
              ></flow-content>
              <div ref="backLayout" class="backgroundLayout">
                <div ref="v_lane" class="v_lane">
                  <lanes
                    :lanes="lanes && lanes.vertical"
                    :checked="checked"
                    @addLane="addLane"
                  ></lanes>
                </div>
                <div ref="h_lane" class="h_lane">
                  <lanes
                    :lanes="lanes && lanes.horizontal"
                    :checked="checked"
                    @addLane="addLane"
                  ></lanes>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="designer-left" style="margin-top: 60px">
        <div class="designer-left-tools">
          <dsf-virtual-scroll>
            <el-collapse v-model="activeNames" accordion>
              <el-collapse-item
                v-for="(tool, index) in flowTools"
                :key="index"
                :title="tool.title"
                :name="index"
              >
                <div class="layui-colla-content layui-show">
                  <div
                    v-for="item in tool.data"
                    :key="item.type"
                    class="ds_tool_item nodeButton"
                    :title="item.name"
                    :sort="item.sort"
                    :nodetype="item.type"
                    draggable="true"
                    @dragstart="dragTool($event, item)"
                  >
                    <img :src="item.img" draggable="false" />
                    {{ item.name }}
                  </div>
                  <div style="clear: both"></div>
                </div>
              </el-collapse-item>
              <el-collapse-item title="边界事件" name="5">
                <div class="layui-colla-content layui-show">
                  <div
                    v-for="event in boundaryEvents"
                    :key="event.type"
                    class="nodeButton ds_tool_event"
                    :title="event.name"
                    :nodetype="event.type"
                  >
                    <div
                      class="tool_event_drag"
                      draggable="true"
                      @dragstart="dragBoundaryEvent($event, event)"
                    >
                      <img
                        :src="event.url"
                        draggable="false"
                        style="width: 32px; height: 32px"
                      />
                    </div>
                    <span class="event-name">{{ event.name }}</span>
                  </div>
                </div>
              </el-collapse-item>
              <el-collapse-item title="附属属性" name="6">
                <div class="layui-colla-content layui-show">
                  <div
                    v-for="event in attached"
                    :key="event.type"
                    class="nodeButton ds_tool_event"
                    :title="event.name"
                    :nodetype="event.type"
                  >
                    <div
                      class="tool_event_drag"
                      draggable="true"
                      @dragstart="dragAttached($event, event)"
                    >
                      <img
                        :src="event.url"
                        draggable="false"
                        style="width: 32px; height: 32px"
                      />
                    </div>
                    <span class="event-name">{{ event.name }}</span>
                  </div>
                </div>
              </el-collapse-item>
            </el-collapse>
          </dsf-virtual-scroll>
        </div>
      </div>
      <!-- <div v-show="flowModel === 'normal'" class="designer-right"> -->
      <div class="designer-right">
        <dsf-virtual-scroll>
          <flow-info
            :checked="checked"
            :nodes="nodes"
            :lines="lines"
            :flow-info="flowInfo"
          ></flow-info>
        </dsf-virtual-scroll>
      </div>
    </div>
  </div>
</template>
<script>
/* eslint-disable no-unused-vars */
/* eslint-disable no-redeclare */
// window.flow = null;
import { mapState } from "vuex";
import FlowFun from "./flow.js";
import Node from "./Node";
import Lane from "./Lane";
import Line from "./Line";
import BoundaryEvent from "./BoundaryEvent";
import FlowInfo from "./flowInfo";
import FlowContent from "./flowContent";
import {
  getObjById,
  getObjByTarget,
  getObjByParentTarget,
  getObjectByTarget,
  getGridPoint,
  deepClone,
  toPx,
  verifySub,
  getAttributes,
  getProperties
} from "./common";
import { nodeSize, _LineType, LaneDirection, tools } from "./tools";
import Lanes from "./Lanes";
import { flowTools, boundaryEvents, attached } from "./propertySource";
import { getNodeLinkPoints } from "./getPoints";
import html2canvas from "html2canvas";
export default {
  name: "Flow",
  components: {
    FlowInfo,
    FlowContent,
    Lanes,
  },
  data() {
    return {
      activeNames: [0],
      isDrawLine: false,
      dragNode: null,
      dragEvent: null,
      dragAttach: null,
      endTarget: null,
      width: 0,
      height: 0,
      maxWidth: 0,
      maxHeight: 0,
      flow: null,
      flowInfo: null,
      sID: this.$route.query && this.$route.query.id,
      nodes: [],
      lines: [],
      events: [],
      distance: 30, // 吸附距离
      cloneObj: null,
      crashNode: null,
      dragSize: 20,
      maxSizeOffset: 50,
      flowTools: flowTools,
      boundaryEvents: boundaryEvents,
      attached: attached,
      checked: null,
      baseLine: {
        show: "",
        top: 0,
        left: 0,
      },
      showLineV: false,
      showLineH: false,
      isGlobalPosMove: false,
      globalPos: null,
      globalMoveLastMouse: null,
      globalRect: null,
      isGlobalSelect: false,
      selectGlobalPos: null,
      selectGlobalPonits: {
        start: { x: 0, y: 0 },
        end: { x: 0, y: 0 },
      },
      globalSelect: null,
      isCtrlKey: false,
      ctrlCopyNodes: null,
      isCopyNodes: false,
      copyNodesPos: null,
      copyNodes: [],
      hasMoved: false,
      isPrintImage: false,
      printImageUrl: "",
    };
  },
  computed: {
    ...mapState({
      page: (state) => {
        return state.flow.page;
      },
      scale: (state) => {
        return state.flow.page.scale;
      },
      lanes: (state) => {
        return state.flow.lanes;
      },
      selectedList: (state) => {
        return state.flow.selectedList;
      },
      flowModel: (state) => {
        return state.flow.flowModel;
      },
    }),
  },
  created() {},
  mounted() {
    this.reset();
    this.init();
  },
  methods: {
    toPx,
    verifySub,
    reset() {
      const ds_page = this.$refs.ds_page;
      this.$store.commit("flow/setPage", {
        width: ds_page.clientWidth,
        height: ds_page.clientHeight,
      });
    },
    async init() {
      await this.initData();
      this.loadElement();
      // 绑定流程画布上的事件
      this.initEvent();
    },
    initData() {
      // 获取flow.json数据 及配置项
      let params = {
        sID: this.sID,
      };
      Object.assign(params, this.$route.query);
      return this.$http
        .get("wfm/getWFDefinition", params)
        .then(async ({data}) => {
          const defaultV = {
            "BaseInfo":{
              "ID":"",
              // "Name": this.$route.query.sName,
              "Name": "",
              "Version":"0.0.0001",
              "TimeLimit":{"Type":"","Value":""},
              "Desc":"",
              "Event":{"onStart":"","onClose":"","onForceClose":"","onExSend":"","onDestory":""},
              "Vendor":{"ID":"","Name":""}
            },
            "NodeList":[],
            "LineList":[],
            "EventList":[],
            "LaneList":{"horizontal":[],"vertical":[]},"ElementList":[]
          };
          this.flowInfo = data?.data?.WorkFlow || defaultV;
          // if (this.$route.query.sName) {
          //   this.flowInfo.BaseInfo.Name = this.$route.query.sName;
          // }
          const properties = await this.getProperties('flow');
          this.flow = {
            attributes: Object.assign(
              getAttributes(properties),
              this.flowInfo
            ),
          };
          // 获取线默认设置
          await getProperties('line');
          this.setFlowModel(data?.data?.flowModel || data?.data?.WorkFlow?.flowModel || "normal");
        })
        .catch((error) => {
          this.flowInfo = {};
        });
    },
    loadElement() {
      const ElementList = (this.flowInfo && this.flowInfo.ElementList) || [];
      if (ElementList && ElementList.length > 0) {
        for (let i = 0; i < ElementList.length; i++) {
          let element = ElementList[i];
          //节点
          if (element.nodeType == "node") {
            let node = element;
            node.in = node.in || [];
            node.out = node.out || [];
            node.point.x = parseFloat(node.point.x);
            node.point.y = parseFloat(node.point.y);
            node = Object.assign(
              {
                x: node.point.x,
                y: node.point.y,
              },
              node
            );
            // 回显 任务类型
            flowTools.forEach((tool) => {
              tool.data.forEach((item) => {
                if (node.attributes.BaseInfo.Type == item.type) {
                  node.attributes.BaseInfo.Type = item.name;
                }
              });
            });
            const newNode = this.addNode(node);
            if (node.nodes) {
              // 显示子节点
              const subNodes = node.nodes;
              newNode.nodes = [];
              newNode.lines = [];
              newNode.events = [];
              subNodes.forEach((item) => {
                item = Object.assign(
                  {
                    x: item.point.x,
                    y: item.point.y,
                  },
                  item
                );
                newNode.nodes.push(this.addSubNode(item, newNode));
              });
            }
            if (node.events) {
              node.events.forEach((event) => {
                let newEvent = new BoundaryEvent(event);
                newEvent.parentNode = node;
                newNode.events.push(newEvent);
              });
            }
            if (node.lines) {
              node.lines.forEach((line) => {
                if (line.linkInfo) {
                  line.linkInfo.fromOffset = parseFloat(
                    line.linkInfo.fromOffset
                  );
                  line.linkInfo.toOffset = parseFloat(line.linkInfo.toOffset);
                }
                line.parentNodeId = node.id;
                // 加到队列里面 等页面渲染后再发通知
                setTimeout(() => {
                  this.$bus.$emit(`addLine${node.id}`, line);
                }, 0);
              });
            }
            this.nodes.push(newNode);
          }
          //线
          else if (element.nodeType == "line") {
            let line = element;
            if (line.linkInfo) {
              line.linkInfo.fromOffset = parseFloat(line.linkInfo.fromOffset);
              line.linkInfo.toOffset = parseFloat(line.linkInfo.toOffset);
            }
            this.$bus.$emit("addLine", line);
          }
          //泳道
          else if (element.nodeType == "lane") {
            const lane = element;
            this.createLane({
              id: lane.id,
              name: lane.name,
              size: lane.size,
              index: lane.index,
              direction: lane.direction,
              attributes: lane.attributes,
            });
          }
          // 边界事件
          else if (element.nodeType == "event") {
            this.events.push(new BoundaryEvent(element));
          }
        }
      }
      this.resize();
    },
    setFlowModel(model) {
      this.$store.commit("flow/setFlowModel", model);
      this.nodes.forEach((item) => {
        item.reset(model);
        this.$bus.$emit("nodeMove", item);
      });
    },
    getSaveData() {
      this.flow.attributes.BaseInfo.ID = this.sID;
      let postData = {
        WorkFlow: {
          BaseInfo: this.flow.attributes.BaseInfo,
          NodeList: [],
          LineList: [],
          EventList: [],
          LaneList: {
            horizontal: [],
            vertical: [],
          },
          ElementList: [],
          flowModel: this.flowModel
        },
      };
      if (this.flowModel === "systematic") {
        postData.sImgData = this.printImageUrl;
      }
      this.nodes.forEach((node) => {
        if (node.lines) {
          let nodeLines = dsf.mix(true, [], node.lines);
          node.lines = nodeLines.map((line) => {
            delete line.paper;
            return line;
          });
        }
        if (node.events) {
          node.events = node.events.map((item) => {
            item.parentNodeId = item.parentNode.id;
            delete item.parentNode;
            return { ...item };
          });
        }
      });
      const info = {
        nodes: [...JSON.parse(JSON.stringify(this.nodes))],
        lanes: { ...this.lanes },
        lines: [...this.lines],
        events: [...this.events],
      };

      //深度复制对象作为流程引擎使用
      let infoCopy = dsf.mix(true, {}, info);
      const copyMap = this.getFlowAllObjectMap(infoCopy);

      let temp = [],
        deleteLines = [];
      //删除多余的线
      for (let i = 0; i < deleteLines.length; i++) {
        for (let n = infoCopy.lines.length - 1; n >= 0; n--) {
          if (infoCopy.lines[n].attributes.ID == deleteLines[i]) {
            infoCopy.lines.splice(n, 1);
          }
        }
      }
      //判断节点特殊操作 结束
      for (let i = 0; i < infoCopy.nodes.length; i++) {
        let node = infoCopy.nodes[i];
        if (node.nodes) {
          node.nodes.forEach((child) => {
            delete child.isRender;
            delete child.edit;
          });
        }

        flowTools.forEach((tool) => {
          tool.data.forEach((item) => {
            if (node.attributes.BaseInfo.Type == item.name) {
              node.attributes.BaseInfo.Type = item.type;
            }
          });
        });
        const y = node.point.y;
        let laneHeight = 0;
        //根据节点的位置和泳道的位置做比较，设置节点在那个泳道中
        node.attributes.BaseInfo.StageID = "";
        node.attributes.BaseInfo.CLaneID = "";
        for (let j = 0; j < info.lanes.horizontal.length; j++) {
          const lane = info.lanes.horizontal[j];
          laneHeight += parseInt(lane.size);

          if (y < laneHeight) {
            node.attributes.BaseInfo.StageID = lane.id;
            node.attributes.BaseInfo.CLaneID = lane.id;
            break;
          }
        }
        const x = node.point.x;
        let laneWidth = 0;
        node.attributes.BaseInfo.LLaneID = "";
        for (let k = 0; k < info.lanes.vertical.length; k++) {
          const lane = info.lanes.vertical[k];
          laneWidth += parseInt(lane.size);

          if (x < laneWidth) {
            node.attributes.BaseInfo.LLaneID = lane.id;
            break;
          }
        }
        postData.WorkFlow.NodeList.push(node.attributes);
        node.nodeType = "node";
        postData.WorkFlow.ElementList.push(node);
      }
      for (let i = 0; i < infoCopy.lines.length; i++) {
        let line = infoCopy.lines[i];
        delete line.paper;
        postData.WorkFlow.LineList.push(line.attributes);
        //整理传递给后台的保存参数
        delete line.arrowEnd;
        delete line.isRender;
        line.nodeType = "line";
        postData.WorkFlow.ElementList.push(line);
      }
      for (let i = 0; i < infoCopy.events.length; i++) {
        let event = infoCopy.events[i];
        postData.WorkFlow.EventList.push(event.attributes);
        //整理传递给后台的保存参数
        event.nodeType = "event";
        postData.WorkFlow.ElementList.push(event);
      }

      for (let i = 0; i < info.lanes.horizontal.length; i++) {
        let lane = info.lanes.horizontal[i];
        let n = deepClone(lane);
        delete n.isRender;
        postData.WorkFlow.LaneList.horizontal.push(lane.attributes);
        n.nodeType = "lane";
        postData.WorkFlow.ElementList.push(n);
      }
      for (let i = 0; i < info.lanes.vertical.length; i++) {
        let lane = info.lanes.vertical[i];
        let n = deepClone(lane);
        delete n.isRender;
        postData.WorkFlow.LaneList.vertical.push(lane.attributes);
        n.nodeType = "lane";
        postData.WorkFlow.ElementList.push(n);
      }
      return postData;
    },
    // 保存
    doSave() {
      // if (this.flowModel === "systematic") {
      //   this.printImage(true);
      //   return;
      // }
      this.saveFlow();
    },
    saveFlow() {
      const postData = this.getSaveData();
      // oa需要保存数据
      // const xmlString = this.getXMLStr();
      const params = {
        sData: JSON.stringify(postData),
        // sXml: xmlString,
      };
      Object.assign(params, {sID: this.$route.query.id}, this.$route.query);
      this.$http
        .post("wfm/saveWFDefinition", params)
        .done((result) => {
          if (result.success) {
            dsf.layer.message("保存成功", true);
          } else {
            dsf.layer.message("保存失败", false);
          }
        })
        .error(() => {
          dsf.layer.message("保存失败", false);
        });
    },
    initEvent() {
      document.addEventListener("dragover", (e) => {
        if (this.dragNode) {
          this.hasMoved = true;
          this.isCtrlKey = e.ctrlKey; // dragend时候 ctrlKey一直是false 故这么解决
          this.dragToolOver(e);
        }
        if (this.dragEvent) {
          this.dragEventOver(e);
        }
        if (this.dragAttach) {
          this.dragAttachedOver(e);
        }
      });
      document.addEventListener("dragend", (e) => {
        if (!this.hasMoved && this.dragNode) {
          this.dragNode && document.body.removeChild(this.dragNode);
          this.dragNode = null;
          this.$bus.$emit("isMoveNode", false);
          this.hasMoved = false;
        }
        if (this.dragNode) {
          this.dragToolEnd(e);
          this.hasMoved = false;
        }
        if (this.dragEvent) {
          this.dragEventEnd(e);
          this.dragEvent && document.body.removeChild(this.dragEvent);
          this.dragEvent = null;
          this.cloneObj = null;
          this.crashNode = null;
          this.$bus.$emit("endMoveEvent", true);
        }
        if (this.dragAttach) {
          this.dragAttachedEnd(e);
          this.dragAttach && document.body.removeChild(this.dragAttach);
          this.dragAttach = null;
          this.cloneObj = null;
          this.crashNode = null;
          this.$bus.$emit("endMoveEvent", true);
        }
      });
      this.$bus.$on("update-baseLine", (baseLine) => {
        this.baseLine = baseLine;
      });
      this.$bus.$on("update-checked", (checked) => {
        this.checked = checked || this.flow;
      });
      this.$bus.$on("parentAutoScroll", this.parentAutoScroll);
      this.$bus.$on("autoScroll", this.autoScroll);
      this.$bus.$on("resize", this.resize);
      this.$bus.$on("dragCloneEvent", this.dragCloneEvent);
      this.$bus.$on("dragCloneAttached", this.dragCloneAttached);
      this.$bus.$on("dragNode", this.dragTool);
      // this.$bus.$on('showSubNode', this.showSubNode);
      this.$bus.$on("startGlobalMove", this.startGlobalMove);
      this.$bus.$on("startCopyNodes", this.startCopyNodes);
      this.$bus.$on("deleteLine", this.deleteLine);
      this.$bus.$on("initSubNode", this.initSubNode);
      document.addEventListener("keydown", this.keydown);
      window.onresize = () => {
        this.reset();
        this.resize();
      };
      this.$refs.root.addEventListener("mousedown", (e) => {
        const selectedList = this.selectedList || null;
        // if (e.ctrlKey && selectedList && selectedList.length > 1) {
        //   // this.startGlobalMove(e);
        //   //如果按住ctrl键，则开始复制多个节点
        // } else {
        // 点击空白处 父父节点不是子流程
        if (
          e?.target?.tagName === "svg" &&
          e?.target?.parentNode?.parentElement?.className.indexOf(
            "subprocess"
          ) === -1
        ) {
          this.checked = this.flow;
          this.lines.forEach((line) => {
            line.edit.isEdit = false;
            line.edit.editName = false;
          });
          this.$bus.$emit("selectGlobal", null);
          this.isGlobalSelect = true;
          const offset = this.$store.getters["flow/getSvgOffset"];
          const mp = this.$store.getters["flow/getMousePointByCanvas"]({
            x: e.pageX,
            y: e.pageY,
          });
          // 基准点是flow div
          this.selectGlobalPos = {
            x: mp.x + offset.left,
            y: mp.y + offset.top,
          };
        }
        // }
      });
      //鼠标移动过程中的操作
      // 载入私有方法(root内鼠标按下事件、root内鼠标移动事件、root内双击编辑事件、泳道插入事件)
      this.$refs.root.addEventListener("mousemove", (e) => {
        if (this.readOnly) {
          return;
        }
        this.setCursor(e);
        if (this.isGlobalPosMove) {
          this.globalMove(e);
        }

        if (this.isGlobalSelect) {
          this.selectGlobalMove(e);
        }

        if (this.isCopyNodes) {
          this.nodesMove(e);
        }
      });
      document.addEventListener("mouseup", (e) => {
        if (this.isGlobalPosMove) {
          this.isGlobalPosMove = false;
          this.globalPos = null;
          this.resize();
          // 删除边界的点
          this.$store.commit("flow/clearPoints");
        }

        if (this.isGlobalSelect) {
          this.selectGlobalEnd(e);
          this.resize();
          // 删除边界的点
          this.$store.commit("flow/clearPoints");
        }

        if (this.isCopyNodes) {
          this.nodesMoveEnd(e);
        }
      });
      // 滚动滑轮事件
      this.$refs.ds_page.addEventListener("wheel", this.scrollwheel, true);
      // 复制事件
      document.addEventListener("copy", (e) => {
        if (this.selectedList.length > 0) {
          this.documentCopy(e);
        }
      });
      document.addEventListener("paste", (e) => {
        if (this.ctrlCopyNodes) {
          this.pasteNode(e);
        }
      });
    },
    initSubNode(node, ElementList) {
      node.open.show = true;
      node.lines &&
        node.lines.forEach((line) => {
          line.remove();
        });
      this.$set(node, "nodes", []);
      this.$set(node, "lines", []);
      this.$set(node, "events", []);
      ElementList.forEach((element) => {
        //节点
        if (element.nodeType == "node") {
          // 显示子节点
          Object.assign(element, {
            x: element.point.x,
            y: element.point.y,
          });
          node.nodes.push(this.addSubNode(element, node));
        }
        //线
        else if (element.nodeType == "line") {
          let line = element;
          if (line.linkInfo) {
            line.linkInfo.fromOffset = parseFloat(line.linkInfo.fromOffset);
            line.linkInfo.toOffset = parseFloat(line.linkInfo.toOffset);
          }
          // 加到队列里面 等页面渲染后再发通知
          setTimeout(() => {
            this.$bus.$emit(`addLine${node.id}`, {
              id: line.id,
              type: line.type,
              name: line.name,
              path: line.path,
              from: line.from,
              fromType: line.fromType,
              to: line.to,
              linkInfo: line.linkInfo,
              attributes: line.attributes,
              parentNodeId: node.id,
            });
          }, 0);
        }
        // 边界事件
        else if (element.nodeType == "event") {
          let newEvent = new BoundaryEvent(element);
          newEvent.parentNode = node;
          newNode.events.push(newEvent);
        }
      });
      this.nodeResize(node);
    },
    copyNewNode(ev, tool, offset, copy) {
      ev?.dataTransfer?.setDragImage?.(new Image(), 0, 0); // 设置 拖动背景图为空
      const target = ev.target || ev;
      let cloneNode = target.cloneNode(true);
      cloneNode.setAttribute("draggable", true);
      cloneNode.offset = {
        x: offset ? offset.x : 0,
        y: offset ? offset.y : 0,
      };
      // 节点之间拖动
      if (copy) {
        cloneNode.copyNode = tool;
      }
      if (!copy && tool.type === 102) {
        cloneNode.tool = tool;
      }
      cloneNode.className =
        cloneNode.className + " drag_nodeOuter " + this.flowModel;
      Object.assign(cloneNode.style, {
        position: "absolute",
        zIndex: 9999,
        top: this.toPx(
          ev.pageY - ((tool.padding || 0) + cloneNode.offset.y) * this.scale
        ),
        left: this.toPx(
          ev.pageX - ((tool.padding || 0) + cloneNode.offset.x) * this.scale
        ),
        pointerEvents: "none",
        cursor: "move",
        transform: `scale(${this.scale}) translateZ(0px)`,
        transformOrigin: "left top",
      });
      document.body.appendChild(cloneNode);
      this.$bus.$emit("isMoveNode", copy ? cloneNode.copyNode : cloneNode);
      return cloneNode;
    },
    startCopyNodes(e) {
      this.isCopyNodes = true;
      this.copyNodesPos = {
        x: e.pageX,
        y: e.pageY,
      };
    },
    createCopyNodes(e) {
      //复制多个节点
      const selectedList = this.selectedList || null;
      if (selectedList && selectedList.length > 1) {
        this.copyNodes = [];
        selectedList.forEach((node) => {
          const target = document.querySelector(
            `.nodeOuter[data-id="${node.id}"]`
          );
          const point = {
            x: node.point.x * this.scale,
            y: node.point.y * this.scale,
          };
          let getPoint = this.$store.getters["flow/getMousePointByPoint"](
            point
          );
          target.pageX = getPoint.x;
          target.pageY = getPoint.y;
          const copyNode = this.copyNewNode(target, node, null, true);
          copyNode.point = getPoint;
          this.copyNodes.push(copyNode);
        });
      }
    },
    dragTool(ev, tool, offset, copy) {
      offset = offset ?? {
        x:
          ev.offsetX -
          (this.page.left / 2 - nodeSize.width - (this.page.left / 2) * 0.05) /
            2,
        y: ev.offsetY - (this.page.left / 2) * 0.05,
      };
      this.dragNode = this.copyNewNode(ev, tool, offset, copy);
    },
    getDisplacement(e) {
      const localMouse = {
        x: e.pageX,
        y: e.pageY,
      };
      const last_p = getGridPoint(this.copyNodesPos);
      const move_p = getGridPoint(localMouse);
      return {
        x: move_p.x - last_p.x,
        y: move_p.y - last_p.y,
      }; // 位移
    },
    nodesMove(e) {
      const displacement = this.getDisplacement(e); // 位移
      if (
        (!this.copyNodes || (this.copyNodes && this.copyNodes.length === 0)) &&
        (Math.abs(displacement.x) > 5 || Math.abs(displacement.y) > 5)
      ) {
        // 当位移大于5
        this.createCopyNodes(e);
      }
      this.copyNodes.forEach((node) => {
        const copyNode = node.copyNode;
        const offset = node.offset;
        const end = {
          x: node.point.x + displacement.x - offset.x * this.scale,
          y: node.point.y + displacement.y - offset.y * this.scale,
        };
        node.style.top = this.toPx(end.y);
        node.style.left = this.toPx(end.x);
      });
      this.isCtrlKey = e.ctrlKey;
    },
    dragToolOver(e) {
      const node = this.dragNode;
      const offset = node.offset;
      node.style.top = this.toPx(e.pageY - offset.y * this.scale);
      node.style.left = this.toPx(e.pageX - offset.x * this.scale);
      const copyNode = node.copyNode;
      this.endTarget = this.getTargetIsNodes(e.target, copyNode || node.tool);
      if (!this.endTarget) {
        return;
      }
      const className = this.endTarget && this.endTarget.className;
      if (className?.indexOf?.("nodes isSub") > -1) {
        const node = getObjByTarget(this.nodes, this.endTarget);
        this.$bus.$emit("dragToSubNode", copyNode !== node ? node : null);
      } else {
        this.$bus.$emit("dragToSubNode", null);
      }
      if (copyNode) {
        this.$bus.$emit(
          `moveCopyNode${copyNode.parentNodeId || ""}`,
          e,
          copyNode
        );
      }
    },
    getTargetIsNodes(target, copyNode) {
      const svgRoot = document.querySelector(".svgRoot");
      if (!svgRoot.contains(target)) {
        return null;
      }
      // 子流程不能拖动到子流程
      if (copyNode && copyNode.type == "102") {
        if (target?.className?.indexOf?.("nodes isSub") > -1) {
          return this.getTargetIsNodes(target?.parentElement);
        }
      }
      if (target?.className?.indexOf("nodes") > -1) {
        return target;
      }
      return this.getTargetIsNodes(target?.parentElement);
    },
    getBeforeCopyLine(nodes) {
      // 获取拷贝节点之间的线
      let lineIds = [];
      nodes.forEach((item) => {
        lineIds = [...lineIds, ...item.out, ...item.in];
      });
      lineIds = [...new Set(lineIds)]; // 去重

      let fromNodeIds = [];
      let toNodeIds = [];
      let newLines = [];
      lineIds.forEach((lineId) => {
        let line = getObjById(this.lines, lineId);
        if (!line) {
          return;
        }
        let from;
        if (line.fromType === "event") {
          from = getObjById(this.events, line.from);
        } else {
          from = getObjById(nodes, line.from);
        }
        const to = getObjById(nodes, line.to);
        if (from && to) {
          // 保证是节点之间的线
          const newLine = deepClone(line);
          newLine.id = tools.uuid();
          fromNodeIds.push({ line: newLine, nodeId: from.id });
          toNodeIds.push({ line: newLine, nodeId: to.id });
          newLines.push(newLine);
        }
      });
      return {
        fromNodeIds,
        toNodeIds,
        newLines,
      };
    },
    resetLineFromAndTo(fromNodeIds, toNodeIds, newNode, copyNode) {
      if (fromNodeIds.length > 0) {
        fromNodeIds.forEach((item) => {
          if (item.nodeId === copyNode.id) {
            newNode.in.push(item.line.id);
            item.line.from = newNode.id;
          }
        });
      }
      if (toNodeIds.length > 0) {
        toNodeIds.forEach((item) => {
          if (item.nodeId === copyNode.id) {
            newNode.out.push(item.line.id);
            item.line.to = newNode.id;
          }
        });
      }
    },
    copyEventsLines(newLines) {
      newLines.forEach((line) => {
        if (line.fromType === "event") {
          // 边界事件
          const event = getObjById(this.events, line.from, "fromId");
          if (event) {
            delete event.fromId;
            line.from = event.id;
          } else {
            line.from = null;
          }
        }
        if (line.from && line.to) {
          this.$bus.$emit("addLine", {
            id: line.id,
            type: line.type,
            name: line.name,
            from: line.from,
            fromType: line.fromType,
            to: line.to,
            linkInfo: line.linkInfo,
          });
        }
      });
    },
    nodesMoveEnd(e) {
      if (this.copyNodes && this.copyNodes.length > 0) {
        const nodes = this.copyNodes.map((item) => item.copyNode);
        const { fromNodeIds, toNodeIds, newLines } = this.getBeforeCopyLine(
          nodes
        );
        const displacement = this.getDisplacement(e);
        const selectedList = [];
        let isMove;
        this.copyNodes.forEach((node) => {
          const copyNode = node.copyNode;
          const offset = node.offset;
          const end = {
            pageX: node.point.x + displacement.x,
            pageY: node.point.y + displacement.y,
          };
          const newNode = this.nodeMoveEnd(end, node);
          if (newNode.id === copyNode.id) {
            isMove = true;
          } else {
            this.resetLineFromAndTo(fromNodeIds, toNodeIds, newNode, copyNode);
          }
          this.checked = newNode;
          selectedList.push(newNode);
        });
        if (!isMove) {
          this.copyEventsLines(newLines);
        }
        // 选中拷贝后的节点
        this.$store.commit("flow/setSelectedList", selectedList);
      }
      this.isCopyNodes = false;
      this.copyNodesPos = null;
      this.copyNodes = [];
      this.$bus.$emit("isMoveNode", false);
    },
    nodeMoveEnd(e, node) {
      let newNode, point, isMove, fromCopyId;
      // 节点的拖动
      const copyNode = node && node.copyNode;
      const offset = node.offset;
      if (copyNode) {
        // 从子流程中拖到流程中
        if (copyNode.parentNodeId) {
          // 删除子流程的节点
          this.deleteNode(copyNode);
          delete copyNode.id;
          delete copyNode.parentNodeId;
          delete copyNode.point;
          point = Object.assign(copyNode, {
            x:
              e.pageX - offset.x * this.scale > copyNode.padding
                ? e.pageX - offset.x * this.scale
                : copyNode.padding,
            y:
              e.pageY - offset.y * this.scale > copyNode.padding
                ? e.pageY - offset.y * this.scale
                : copyNode.padding,
            mouseConvert: true,
          });
        } else {
          // 流程位置移动
          const mp = this.$store.getters["flow/getMousePointByCanvas"]({
            x: e.pageX - offset.x * this.scale,
            y: e.pageY - offset.y * this.scale,
          });
          // let p = getGridPoint(mp);
          let p = mp;
          p = {
            x: p.x > copyNode.padding ? p.x : copyNode.padding,
            y: p.y > copyNode.padding ? p.y : copyNode.padding,
          };
          // 按住ctrl 就是复制
          if (this.isCtrlKey) {
            // 复制一个节点
            fromCopyId = copyNode.id;
            const id = tools.uuid();
            const newNode = JSON.parse(JSON.stringify(copyNode));
            point = Object.assign({}, newNode, {
              id: id,
              out: [],
              in: [],
              x: p.x,
              y: p.y,
            });
            point.attributes.BaseInfo.ID = id;
          } else {
            // 缩放后的坐标
            p = {
              x: p.x / this.scale,
              y: p.y / this.scale,
            };
            // 只是移动位置
            p = getGridPoint(p);
            copyNode.point.x = p.x;
            copyNode.point.y = p.y;
            this.$bus.$emit("nodeMove", copyNode);
            isMove = true;
          }
        }
      }
      if (!isMove) {
        // 从环节类型中拖动到流程
        const type = e.target && e.target.getAttribute("nodetype");
        const sort = e.target && e.target.getAttribute("sort");
        point = point || {
          type: type,
          sort: sort,
          x: e.pageX - offset.x * this.scale,
          y: e.pageY - offset.y * this.scale,
          mouseConvert: true,
        };
        newNode = this.copyNodeNext(point, fromCopyId, copyNode);
      }
      document.body.removeChild(node);
      this.resize();
      return isMove ? copyNode : newNode;
    },
    copyNodeNext(point, fromCopyId, copyNode) {
      const node = this.addNode(point);
      this.nodes.push(node);
      if (fromCopyId) {
        // 节点复制后 复制边界事件
        this.copyNodeEvent(fromCopyId, node);
      }
      // 子流程拷贝后 子节点复制
      if (copyNode && copyNode.nodes && copyNode.nodes.length > 0) {
        node.nodes = [];
        copyNode.nodes.forEach((item) => {
          const subNode = deepClone(item);
          const subPoint = Object.assign(
            {
              x: subNode.point.x,
              y: subNode.point.y,
            },
            subNode
          );
          node.nodes.push(this.addSubNode(subPoint, node));
        });
      }
      return node;
    },
    dragToolEnd(e) {
      if (this.endTarget) {
        const className = this.endTarget && this.endTarget.className;
        const node = getObjByTarget(this.nodes, this.endTarget);
        if (className?.indexOf?.("nodes isSub") > -1 && !this.verifySub(node)) {
          if (node) {
            let point, isMove;
            // 节点的拖动
            if (this.dragNode && this.dragNode.copyNode) {
              const copyNode = this.dragNode.copyNode;
              const offset = this.dragNode.offset;
              // 从流程中拖到子流程中 或者子流程到宁外一个子流程
              if (
                !copyNode.parentNodeId ||
                (copyNode.parentNodeId && copyNode.parentNodeId !== node.id)
              ) {
                point = Object.assign(copyNode, {
                  x:
                    e.pageX - offset.x * this.scale > copyNode.padding
                      ? e.pageX - offset.x * this.scale
                      : copyNode.padding,
                  y:
                    e.pageY - offset.y * this.scale > copyNode.padding
                      ? e.pageY - offset.y * this.scale
                      : copyNode.padding,
                  mouseConvert: true,
                });
                this.deleteNode(copyNode);
                delete copyNode.id;
              } else {
                // 在当前子流程中 只是位置移动
                point = {
                  x: e.pageX - offset.x * this.scale,
                  y: e.pageY - offset.y * this.scale,
                  mouseConvert: true,
                };
                const options = this.getOptions(point);
                copyNode.point.x = options.x - node.point.x;
                // 子节点相对于父节点的位置
                copyNode.point.y =
                  options.y - node.point.y - node.imgHeight - node.padding * 2;
                this.$bus.$emit(`nodeMove${copyNode.parentNodeId}`, copyNode);
                this.$bus.$emit(`cutOffLine${copyNode.parentNodeId}`, copyNode);
                isMove = true;
              }
            }
            if (!isMove) {
              // 从环节类型中拖动到子流程
              const type = e.target && e.target.getAttribute("nodetype");
              const sort = e.target && e.target.getAttribute("sort");
              point = point || {
                type: type,
                sort: sort,
                x: e.pageX,
                y: e.pageY,
                mouseConvert: true,
              };
              const subNode = this.addSubNode(point, node);
              node.nodes
                ? node.nodes.push(subNode)
                : this.$set(node, "nodes", [subNode]);
              this.$set(node, "lines", node.lines || []);
              this.$set(node, "events", node.events || []);
              this.nodeResize(node);
              this.$bus.$emit(`cutOffLine${subNode.parentNodeId}`, subNode);
            }
          }
          document.body.removeChild(this.dragNode);
        } else if (
          className?.indexOf("nodes") > -1 ||
          (className?.indexOf?.("nodes isSub") > -1 && this.verifySub(node))
        ) {
          this.checked = this.nodeMoveEnd(e, this.dragNode);
          this.$bus.$emit("cutOffLine", this.checked);
          this.$store.commit("flow/setSelectedList", [this.checked]);
        }
      } else {
        document.body.removeChild(this.dragNode);
      }
      this.dragNode = null;
      this.$bus.$emit("isMoveNode", false);
    },
    createDragNode(ev, event) {
      // this.checked = this.flow;
      ev?.dataTransfer?.setDragImage?.(new Image(), 0, 0); // 设置 拖动背景图为空
      let dragNode = ev.target.cloneNode(true);
      dragNode.className = "ds_tool_event_drag";
      dragNode.setAttribute("draggable", false);
      dragNode.offset = {
        x: ev.offsetX,
        y: ev.offsetY,
      };
      const cloneTool = deepClone(event);
      this.cloneObj = new BoundaryEvent(cloneTool, ev);
      Object.assign(dragNode.style, {
        position: "absolute",
        zIndex: 9999,
        width: this.toPx(this.cloneObj.width),
        height: this.toPx(this.cloneObj.height),
        top: this.toPx(ev.pageY - ev.offsetY),
        left: this.toPx(ev.pageX - ev.offsetX),
        pointerEvents: "none",
        transform: `scale(${this.scale}) translateZ(0px)`,
        transformOrigin: "left top",
      });
      document.body.appendChild(dragNode);
      return dragNode;
    },
    commonDragOver(e, dragNode) {
      e.dataTransfer.dropEffect = "copy";
      if (dragNode) {
        const offset = dragNode.offset;
        // 拖动边界事件
        const comp = this.computedPoint(e, dragNode);
        if (
          Array.from(e?.target?.classList)?.indexOf("task-node") > -1 ||
          Array.from(e?.target?.parentElement)?.indexOf("node")
        ) {
          const parentAndSub = getObjByParentTarget(
            this.nodes,
            "nodes",
            e.target
          );
          if (parentAndSub && !this.verifySub(parentAndSub.parent)) {
            const parentNode = parentAndSub.parent;
            const subNode = parentAndSub.sub;
            this.crashNode = subNode;
            let points;
            if (
              this.cloneObj.sort === "boundaryEvent" &&
              subNode.sort !== "task"
            ) {
              points = [];
            } else {
              points = this.$store.getters["flow/getPoints"](subNode.id);
            }
            if (!points) {
              this.$store.commit("flow/setPointsByNode", {
                node: subNode,
                obj: this.cloneObj,
              });
              points = this.$store.getters["flow/getPoints"](subNode.id);
            }
            const subComp = {
              point: {
                x: comp.point.x - parentNode.point.x * this.scale,
                y:
                  comp.point.y -
                  (parentNode.point.y +
                    parentNode.padding +
                    parentNode.imgHeight +
                    parentNode.padding * 2) *
                    this.scale,
              },
            };
            const point = this.getPoint(subComp, points, this.distance);
            if (point) {
              this.cloneObj = Object.assign(this.cloneObj, point, {
                parentNode,
              });
              let getPoint = this.$store.getters["flow/getMousePointByPoint"](
                point.point
              );
              getPoint.x = getPoint.x + parentNode.point.x * this.scale;
              getPoint.y =
                getPoint.y +
                (parentNode.point.y +
                  parentNode.imgHeight +
                  parentNode.padding * 2) *
                  this.scale;
              dragNode.style.top = this.toPx(getPoint.y);
              dragNode.style.left = this.toPx(getPoint.x);
              return;
            }
          }
          const node = getObjByTarget(this.nodes, e.target);
          // 父节点吸附
          if (node) {
            this.crashNode = node;
            let points;
            if (
              this.cloneObj.sort === "boundaryEvent" &&
              node.sort !== "task"
            ) {
              points = [];
            } else {
              points = this.$store.getters["flow/getPoints"](node.id);
            }
            if (!points) {
              this.$store.commit("flow/setPointsByNode", {
                node,
                obj: this.cloneObj,
              });
              points = this.$store.getters["flow/getPoints"](node.id);
            }
            const point = this.getPoint(comp, points, this.distance);
            if (point) {
              this.cloneObj = Object.assign(this.cloneObj, point);
              const getPoint = this.$store.getters["flow/getMousePointByPoint"](
                point.point
              );
              dragNode.style.top = this.toPx(getPoint.y);
              dragNode.style.left = this.toPx(getPoint.x);
              return;
            }
          }
        }
        this.crashNode = null;
        dragNode.style.top = this.toPx(e.pageY - offset.x * this.scale);
        dragNode.style.left = this.toPx(e.pageX - offset.x * this.scale);
      }
    },
    commonDragEnd(e) {
      if (e.type === "drop") {
        e = event ? event : e;
        e.preventDefault();
      } else {
        if (event.touches) {
          e = event.touches[0] ? event.touches[0] : event;
        }
      }
      if (!this.crashNode) {
        return;
      }
      const points =
        this.$store.getters["flow/getPoints"](this.crashNode.id) ?? [];
      let obj = this.computedPoint(e, this.cloneObj);
      if (this.cloneObj.parentNode) {
        const parentNode = this.cloneObj.parentNode;
        obj = {
          point: {
            x: obj.point.x - parentNode.point.x * this.scale,
            y:
              obj.point.y -
              (parentNode.point.y +
                parentNode.padding +
                parentNode.imgHeight +
                parentNode.padding * 2) *
                this.scale,
          },
        };
      }
      const point = this.getPoint(obj, points, this.distance);
      if (point) {
        // 删除吸附点
        points.splice(points?.indexOf(point), 1);
        // 当前node上面移动位置
        if (this.cloneObj.from) {
          const events = this.cloneObj.parentNode
            ? this.cloneObj.parentNode.events
            : this.events;
          events.forEach((item) => {
            if (item.id === this.cloneObj.from) {
              // 恢复可吸附点
              this.$store.commit("flow/addPoint", {
                id: this.cloneObj.nodeId,
                obj: {
                  point: {
                    x: item.point.x,
                    y: item.point.y,
                  },
                  index: item.index,
                  direction: item.direction,
                },
              });
            }
          });
          this.deleteEvent(this.cloneObj.from, events);
          this.cloneObj.id = this.cloneObj.from;
          delete this.cloneObj.from;
        }
        // 最后位置
        this.cloneObj = Object.assign(this.cloneObj, point);
        this.cloneObj.point.x = this.cloneObj.point.x / this.scale;
        this.cloneObj.point.y = this.cloneObj.point.y / this.scale;
        this.cloneObj.nodeId = this.crashNode.id;
        this.checked = this.cloneObj;
        if (this.cloneObj.out && this.cloneObj.out.length > 0) {
          this.$bus.$emit(
            `eventMove${
              this.cloneObj.parentNode ? this.cloneObj.parentNode.id : ""
            }`,
            this.cloneObj
          );
        }
      }
    },
    dragBoundaryEvent(ev, event) {
      this.dragEvent = this.createDragNode(ev, event);
      this.$bus.$emit("startMoveEvent", this.dragEvent);
    },
    dragEventOver(e) {
      this.commonDragOver(e, this.dragEvent);
    },
    dragEventEnd(e) {
      this.commonDragEnd(e);
      if (this.crashNode) {
        this.addNewEvent(this.cloneObj);
      }
    },
    addNewEvent(cloneObj) {
      if (cloneObj.parentNode) {
        this.nodes.forEach((node) => {
          if (cloneObj.parentNode && cloneObj.parentNode.id === node.id) {
            node.events.push(cloneObj);
          }
        });
      } else {
        this.events.push(cloneObj);
      }
    },
    dragAttached(ev, event) {
      let newEvent = deepClone(event);
      newEvent.name = event.type === "mark" ? "A" : 1;
      this.dragAttach = this.createDragNode(ev, newEvent);
      this.$bus.$emit("startMoveEvent", this.cloneObj);
    },
    dragAttachedOver(e) {
      this.commonDragOver(e, this.dragAttach);
    },
    dragAttachedEnd(e) {
      this.commonDragEnd(e);
      if (this.crashNode) {
        this.addNewEvent(this.cloneObj);
      }
    },
    dragCloneEvent(e, event) {
      const copyEvent = deepClone(event);
      copyEvent.from = event.id;
      copyEvent.id = tools.uuid();
      this.dragBoundaryEvent(e, copyEvent);
    },
    dragLineProperty(e, event) {
      const copyEvent = deepClone(event);
      copyEvent.from = event.id;
      copyEvent.id = tools.uuid();
      this.dragBoundaryEvent(e, copyEvent);
    },
    // 节点拷贝后 拷贝边界事件
    copyNodeEvent(fromCopyId, node) {
      let copyEvents = [];
      this.events.forEach((ev) => {
        if (ev.nodeId === fromCopyId) {
          copyEvents.push(deepClone(ev));
        }
      });
      if (copyEvents.length === 0) {
        return;
      }
      // 获取边界事件坐标点
      this.$store.commit("flow/setPointsByNode", { node, obj: copyEvents[0] });
      const points = this.$store.getters["flow/getPoints"](node.id);
      points.forEach((item) => {
        copyEvents.forEach((ev) => {
          if (ev.direction === item.direction && ev.index === item.index) {
            ev.fromId = ev.id; // 用于画线
            ev.id = tools.uuid();
            ev.point = item.point;
            ev.nodeId = node.id;
            this.addNewEvent(ev);
          }
        });
      });
    },
    getPoint(obj, all, distance) {
      let point;
      let min = 0;
      all.forEach((item) => {
        let realDistance = this.getDistance(item, obj);
        if (realDistance <= distance) {
          if (min < realDistance) {
            min = realDistance;
            point = item;
          }
        }
      });
      return point;
    },
    getDistance(p1, p2) {
      const dis = Math.sqrt(
        Math.pow(p1.point.x - p2.point.x, 2) +
          Math.pow(p1.point.y - p2.point.y, 2)
      );
      return dis;
    },
    computedPoint(e, moveObj) {
      const mp = this.$store.getters["flow/getMousePointByCanvas"]({
        x: e.pageX,
        y: e.pageY,
      });
      return { point: getGridPoint(mp) };
    },
    getOptions(opts) {
      const options = Object.assign(
        {
          id: tools.uuid(),
          type: 1,
        },
        opts
      );
      let p = {
        x: options.x || 0,
        y: options.y || 0,
      };
      if (options.mouseConvert == true) {
        const mp = this.$store.getters["flow/getMousePointByCanvas"]({
          x: options.x,
          y: options.y,
        });
        p.x = mp.x;
        p.y = mp.y;
      }
      p = getGridPoint(p);
      options.x = p.x / this.scale;
      options.y = p.y / this.scale;
      return options;
    },
    // 增加节点
    addNode(opts) {
      const options = this.getOptions(opts);
      const type = options.type;
      if (!options.name && type != 102) {
        options.order = this.nodes.length + 1;
      }
      options.name =
        options.name ||
        (type === "201" ? "开始" : type === "299" ? "结束" : "") ||
        "新建环节" + this.nodes.length;
      const node = new Node(options);
      node.reset(this.flowModel);
      return node;
    },
    // 增加子流程子节点
    addSubNode(opts, node) {
      const options = this.getOptions(opts);
      options.name =
        options.name || "新建环节" + ((node.nodes && node.nodes.length) || 0);
      if (options.mouseConvert == true) {
        options.x = options.x - node.point.x;
        // 子节点相对于父节点的位置
        options.y =
          options.y - node.point.y - node.imgHeight - node.padding * 2;
      }
      let children = new Node(options);
      children.parentNodeId = node.id; // 子流程才有
      return children;
    },
    showSubNode(node) {},
    // 监听键盘删除事件
    keydown(e) {
      if (this.checked && this.checked.readOnly) {
        return;
      }
      if (e.keyCode == 46) {
        const selectedList = this.selectedList || null;
        // 多选才删除
        if (selectedList && selectedList.length > 1) {
          selectedList.forEach((node) => {
            setTimeout(() => {
              this.deleteNode(node);
            }, 0);
          });
        } else if (this.checked instanceof Node) {
          // 删除单个
          this.deleteNode(this.checked, true);
        } else if (this.checked instanceof Lane) {
          this.$store.commit("flow/deleteLane", this.checked);
          this.resize();
        } else if (this.checked instanceof Line) {
          this.deleteLine(this.checked, true);
        } else if (this.checked instanceof BoundaryEvent) {
          const checked = this.checked;
          if (checked.type === "risk" || checked.type === "mark") {
            dsf.layer
              .confirm({
                title: "",
                message: "确认删除？",
              })
              .then(() => {
                this.deleteEventAndLines(checked);
              })
              .catch(() => {});
          } else {
            this.deleteEventAndLines(checked);
          }
        }
        this.checked = {};
      }
    },
    deleteEventAndLines(checked) {
      for (let i = checked["out"].length - 1; i >= 0; i--) {
        const l = getObjById(this.lines, checked["out"][i]);
        if (l) {
          this.deleteLine(l);
        }
      }
      const events = checked.parentNode
        ? checked.parentNode.events
        : this.events;
      // 删除单个
      this.deleteEvent(checked.id, events, true);
    },
    deleteNode(node, isDel) {
      // 删除子流程节点
      let parentNode;
      if (node.parentNodeId) {
        parentNode = this.nodes.find((item) => node.parentNodeId === item.id);
        if (isDel && this.verifySub(parentNode)) {
          return;
        }
      }
      const nodes = node.parentNodeId ? parentNode.nodes : this.nodes;
      const lines = node.parentNodeId ? parentNode.lines : this.lines;
      const events = node.parentNodeId ? parentNode.events : this.events;
      const index = nodes?.indexOf(node);
      if (index > -1) {
        const obj = node;
        // 把线连接起来
        if (obj.in.length > 0 && obj.out.length > 0) {
          obj.in.forEach((lineIn) => {
            const getIn = getObjById(lines, lineIn);
            if (getIn) {
              const fromNode = getObjById(nodes, getIn.from);
              obj.out.forEach((lineOut) => {
                const getOut = getObjById(lines, lineOut);
                if (getOut) {
                  const toNode = getObjById(nodes, getOut.to);
                  if (fromNode === toNode) {
                    return;
                  }
                  // 画线
                  const id = tools.uuid();
                  const currentLineInfo = {
                    endPoint: getOut.path[getOut.path.length - 1],
                    fromDirection: getIn.linkInfo.fromDirection,
                    fromNode: fromNode,
                    startPoint: getIn.path[0],
                    targetDirection: getOut.linkInfo.toDirection,
                    targetNode: toNode,
                  };
                  const points = getNodeLinkPoints(currentLineInfo);
                  // getOut.path.splice(0, 1);
                  // getIn.path.splice(getIn.path.length - 1, 1, ...getOut.path);
                  let newLineParam = {
                    id: id,
                    type: getIn.type,
                    name: fromNode.name + "→" + toNode.name,
                    path: points,
                    from: getIn.from,
                    fromType: getIn.fromType,
                    isHide: getIn.isHide,
                    to: getOut.to,
                    linkInfo: {
                      fromDirection: getIn.linkInfo.fromDirection,
                      fromOffset: getIn.linkInfo.fromOffset,
                      toDirection: getOut.linkInfo.toDirection,
                      toOffset: getOut.linkInfo.toOffset,
                    },
                    attributes: Object.assign(getIn.attributes, {
                      BaseInfo: {
                        EndNodeID: getOut.to,
                        ID: id,
                        Name: fromNode.name + "→" + toNode.name,
                        StartNodeID: getIn.from,
                      },
                    }),
                  };
                  if (node.parentNodeId) {
                    newLineParam.parentNodeId = node.parentNodeId;
                  }
                  this.$bus.$emit(
                    `addLine${node.parentNodeId || ""}`,
                    newLineParam
                  );
                }
              });
            }
          });
        }
        // 删除线
        for (let i = obj.out.length - 1; i >= 0; i--) {
          const l = getObjById(lines, obj.out[i]);
          if (l) {
            this.deleteLine(l);
          }
        }
        for (let i = obj.in.length - 1; i >= 0; i--) {
          const l = getObjById(lines, obj.in[i]);
          if (l) {
            this.deleteLine(l);
          }
        }

        // 删除边界事件
        for (let i = 0; i < events.length; i++) {
          let nodeId = events[i].nodeId;
          if (nodeId === obj.id) {
            let event = events[i];
            // 删除线
            for (let j = event.out.length - 1; j >= 0; j--) {
              const line = getObjById(this.lines, event.out[j]);
              if (line) {
                this.deleteLine(line);
              }
            }
            events.splice(i, 1);
            i--;
          }
        }
        // 删除节点
        nodes.splice(index, 1);
      }
    },
    deleteLine(line, isDel) {
      // 删除子流程的线
      let parentNode;
      if (line.parentNodeId) {
        parentNode = this.nodes.find((item) => line.parentNodeId === item.id);
        if (isDel && this.verifySub(parentNode)) {
          return;
        }
      }
      line.remove();
      const nodes = line.parentNodeId ? parentNode.nodes : this.nodes;
      const lines = line.parentNodeId ? parentNode.lines : this.lines;
      const index = lines?.indexOf(line);
      if (index > -1) {
        this.deleteFromOrOut(nodes, lines, line, index);
      }
    },
    deleteFromOrOut(nodes, lines, line, index) {
      const from = getObjById(nodes, line.from);
      const to = getObjById(nodes, line.to);
      if (from) {
        const f_index = from["out"]?.indexOf(line.id);
        if (f_index >= 0) {
          from["out"].splice(f_index, 1);
        }
      }
      if (to) {
        const t_index = to["in"]?.indexOf(line.id);
        if (t_index >= 0) {
          to["in"].splice(t_index, 1);
        }
      }
      lines.splice(index, 1);
      this.$bus.$emit("updateIntersection");
    },
    deleteEvent(id, events, isDel) {
      const index = (events ? events : this.events).findIndex(
        (ev) => ev.id === id
      );
      index > -1 && (events ? events : this.events).splice(index, 1);
      // 恢复可吸附点
      this.$store.commit("flow/addPoint", {
        id: this.checked.nodeId,
        obj: {
          point: {
            x: this.checked.point.x,
            y: this.checked.point.y,
          },
          index: this.checked.index,
          direction: this.checked.direction,
        },
      });
      // this.dragEvent && document.body.removeChild(this.dragEvent);
    },
    // 添加泳道
    addLane(direction, index) {
      this.createLane({
        direction: direction,
        index: index,
      });
    },
    createLane(options) {
      const direction = options.direction;
      options.name =
        options.name || "泳道" + (this.lanes[direction].length + 1);
      options.id = options.id || tools.uuid();
      const lane = new Lane(options);
      const before = this.lanes[direction][
        (options.index || this.lanes[direction].length) - 1
      ];
      if (direction == LaneDirection.vertical) {
        // 水平方向
        lane.left = before ? before.size + before.left : 0;
      } else {
        // 垂直方向
        lane.top = before ? before.size + before.top : 0;
      }
      this.$store.commit("flow/addLane", {
        direction,
        lane,
        index: options.index,
      });
      this.resize();
    },
    onScroll(e) {
      const target = e.target;
      this.$store.commit("flow/setSvgRoot", {
        scrollTop: target.scrollTop,
        scrollLeft: target.scrollLeft,
      });
    },
    //获取上一步节点
    getFromNodes(node) {
      if (!node["in"] || node["in"].length <= 0) {
        return null;
      }
      var arr = [];
      for (let i = 0; i < node["in"].length; i++) {
        var lineId = node["in"][i];
        var line = getObjById(this.lines, lineId);
        if (line) {
          var fn = getObjById(this.nodes, line.from);
          arr.push(fn);
        }
      }
      return arr;
    },
    //获取下一步节点
    getToNodes(node) {
      if (!node["out"] || node["out"].length <= 0) {
        return null;
      }
      var arr = [];
      for (let i = 0; i < node["out"].length; i++) {
        var lineId = node["out"][i];
        var line = getObjById(this.lines, lineId);
        if (line) {
          var fn = getObjById(this.nodes, line.to);
          arr.push(fn);
        }
      }
      return arr;
    },
    getFlowAllObjectMap(info) {
      var dm = {};
      for (let i = 0; i < info.nodes.length; i++) {
        var node = info.nodes[i];
        dm[node.id] = node;
      }
      for (let i = 0; i < info.lines.length; i++) {
        var line = info.lines[i];
        dm[line.id] = line;
      }
      for (let i = 0; i < info.lanes.horizontal.length; i++) {
        var lane = info.lanes.horizontal[i];
        dm[lane.id] = lane;
      }
      for (let i = 0; i < info.lanes.vertical.length; i++) {
        var lane = info.lanes.vertical[i];
        dm[lane.id] = lane;
      }
      return dm;
    },
    drawLine() {
      this.isDrawLine = !this.isDrawLine;
    },
    resize() {
      const minWidth = this.page.width;
      const minHeight = this.page.height;
      const width = minWidth || 0;
      const height = minHeight || 0;

      const lastV = this.lanes.vertical[this.lanes.vertical.length - 1];
      // 水平方向泳道标题的宽度
      const horizontalWidth = this.$store.getters["flow/lanesWidth"];
      // 泳道水平长度超出
      if (lastV && lastV.size + lastV.left + horizontalWidth > width) {
        this.width = lastV.size + lastV.left + horizontalWidth;
      } else {
        this.width = minWidth;
      }

      const lastH = this.lanes.horizontal[this.lanes.horizontal.length - 1];
      // 垂直方向泳道标题的宽度
      const verticalHeight = this.$store.getters["flow/lanesHeight"];
      // 泳道垂直长度超出
      if (lastH && lastH.size + lastH.top + verticalHeight > height) {
        this.height = lastH.size + lastH.top + verticalHeight;
      } else {
        this.height = minHeight;
      }

      // 和最长的节点宽度比较
      let maxWidth = 0;
      let maxHeigth = 0;
      this.nodes.forEach((node) => {
        if (node.width + node.point.x + node.padding * 2 > maxWidth) {
          maxWidth = node.width + node.point.x + node.padding * 2;
        }
        if (node.height + node.point.y + node.padding * 2 + 20 > maxHeigth) {
          maxHeigth = node.height + node.point.y + node.padding * 2 + 20;
        }
      });
      this.width = this.width > maxWidth ? this.width : maxWidth;
      this.height = this.height > maxHeigth ? this.height : maxHeigth;
    },
    nodeResize(node) {
      // 和最长的节点宽度比较
      let maxWidth = 0;
      let maxHeigth = 0;
      node.nodes.forEach((item) => {
        if (item.width + item.point.x + item.padding * 2 > maxWidth) {
          maxWidth = item.width + item.point.x + item.padding * 2;
        }
        if (
          item.height +
            item.point.y +
            item.padding * 2 +
            20 +
            node.imgHeight +
            node.padding * 2 >
          maxHeigth
        ) {
          // 节点高度 + 节点纵坐标 + padding + 名称高度 + 父节点占用高度
          maxHeigth =
            item.height +
            item.point.y +
            item.padding * 2 +
            20 +
            node.imgHeight +
            node.padding * 2;
        }
      });
      node.width = node.width > maxWidth ? node.width : maxWidth;
      node.height = node.height > maxHeigth ? node.height : maxHeigth;
      this.$bus.$emit("nodeMove", node);
    },
    autoScroll(arg) {
      let point = null;
      const type = arg.type;
      if (type == "node") {
        point = arg.point;
        if (arg.mouse.x > this.width / 2) {
          point.x = point.x + arg.data.width + this.maxSizeOffset;
        } else {
          point.x = point.x;
        }
        if (arg.mouse.y > this.height / 2) {
          point.y = point.y + arg.data.height + this.maxSizeOffset;
        } else {
          point.y = point.y;
        }
      } else if (type == "line") {
        point = this.$store.getters["flow/getMousePointByCanvas"]({
          x: arg.mouse.x,
          y: arg.mouse.y,
        });
        if (arg.mouse.x > this.width / 2) {
          point.x = point.x + this.maxSizeOffset;
        } else {
          point.x = point.x - this.maxSizeOffset;
        }
        if (arg.mouse.y > this.height / 2) {
          point.y = point.y + this.maxSizeOffset;
        } else {
          point.y = point.y - this.maxSizeOffset;
        }
      } else if (type == "lane") {
        point = this.$store.getters["flow/getMousePointByCanvas"]({
          x: arg.mouse.x,
          y: arg.mouse.y,
        });
        if (arg.mouse.x > this.width / 2) {
          point.x = point.x + this.maxSizeOffset;
        } else {
          point.x = point.x;
        }
        if (arg.mouse.y > this.height / 2) {
          point.y = point.y + this.maxSizeOffset;
        } else {
          point.y = point.y;
        }
      }
      //滚动条自动回滚
      let parentElement = this.$refs.ds_page;
      if (!parentElement) {
        return;
      }
      let size, scrollLeft, scrollTop;
      if (point.x < parentElement.scrollLeft) {
        size = Math.abs(parentElement.scrollLeft - point.x);
        scrollLeft = parentElement.scrollLeft;
        parentElement.scrollLeft = scrollLeft - size / 3;
      }
      if (point.y < parentElement.scrollTop) {
        size = Math.abs(parentElement.scrollTop - point.y);
        scrollTop = parentElement.scrollTop;
        parentElement.scrollTop = scrollTop - size / 3;
      }
      if (point.h < parentElement.scrollTop) {
        size = Math.abs(parentElement.scrollLeft - point.x);
        scrollLeft = parentElement.scrollLeft;
        parentElement.scrollLeft = scrollLeft - size / 3;
      }
      //滚动条自动延伸
      if (point.x > parentElement.clientWidth + parentElement.scrollLeft) {
        this.width = point.x;
        size = Math.abs(
          parentElement.clientWidth + parentElement.scrollLeft - point.x
        );
        scrollLeft = parentElement.scrollLeft;
        parentElement.scrollLeft = scrollLeft + size / 3;
      }
      if (point.y > parentElement.clientHeight + parentElement.scrollTop) {
        this.height = point.y;
        size = Math.abs(
          parentElement.clientHeight + parentElement.scrollTop - point.y
        );
        scrollTop = parentElement.scrollTop;
        parentElement.scrollTop = scrollTop + size / 3;
      }
    },
    parentAutoScroll(arg) {
      // 计算有问题 先不管
      let mouse = arg.mouse;
      let point = arg.point;
      let parent = arg.parent;
      let node = arg.node;
      if (point.x > parent.width + parent.point.x) {
        point.x = point.x + node.width + this.maxSizeOffset;
      } else {
        point.x = point.x;
      }
      if (point.y > parent.height + parent.point.y) {
        point.y = point.y + node.height + this.maxSizeOffset;
      } else {
        point.y = point.y;
      }
      //滚动条自动延伸
      if (point.x > parent.width + 0) {
        parent.width = point.x;
      }
      if (point.y > parent.height + 0) {
        parent.height = point.y;
      }
      this.$bus.$emit("nodeMove", parent);
      this.autoScroll({
        type: "node",
        data: parent,
        mouse: {
          x: mouse.x,
          y: mouse.y,
        },
        point: {
          x: point.x,
          y: point.y,
        },
      });
    },
    setCursor(evt) {
      if (evt.target.tagName != "svg") {
        var className = evt.target.className || "";
        if (evt.target.tagName == "path") {
          var segmen = getObjectByTarget(evt.target);
          if (segmen) {
            var mp = this.$store.getters["flow/getMousePointByCanvas"]({
              x: evt.pageX,
              y: evt.pageY,
            });
            if (segmen.type == _LineType.horizontal) {
              if (
                segmen.index == 0 &&
                Math.abs(segmen.sp.x - mp.x) <= this.dragSize
              ) {
                this.setRootCursor("move");
              } else if (
                segmen.isLast &&
                Math.abs(segmen.ep.x - mp.x) <= this.dragSize
              ) {
                this.setRootCursor("move");
              } else {
                this.setRootCursor("ns-resize");
              }
            } else {
              if (
                segmen.index == 0 &&
                Math.abs(segmen.sp.y - mp.y) <= this.dragSize
              ) {
                this.setRootCursor("move");
              } else if (
                segmen.isLast &&
                Math.abs(segmen.ep.y - mp.y) <= this.dragSize
              ) {
                this.setRootCursor("move");
              } else {
                this.setRootCursor("ew-resize");
              }
            }
          }
        }
      } else {
        this.setRootCursor("default");
      }
    },
    setRootCursor(type) {
      this.$refs.root.style.cursor = type;
    },
    startGlobalMove(e) {
      //如果按住shift键，则启动全局位移功能
      this.isGlobalPosMove = true;
      this.globalPos = this.$store.getters["flow/getMousePointByCanvas"]({
        x: e.pageX,
        y: e.pageY,
      });
      this.globalMoveLastMouse = {
        x: e.pageX,
        y: e.pageY,
      };
      this.globalRect = this.getRect();
    },
    globalMove(evt) {
      if (this.isGlobalPosMove) {
        const localMouse = {
          x: evt.pageX,
          y: evt.pageY,
        };
        const last_p = getGridPoint(this.globalMoveLastMouse);
        const move_p = getGridPoint(localMouse);
        const current_p = {
          x: move_p.x - last_p.x,
          y: move_p.y - last_p.y,
        };
        const maxWidth = Number.MIN_VALUE,
          maxHeight = Number.MIN_VALUE;
        if (current_p.x == 0 && current_p.y == 0) {
          return false;
        }
        let x_move = false,
          y_move = false;
        if (this.globalRect.x + current_p.x > 0) {
          this.globalRect.x += current_p.x;
          x_move = true;
        }
        if (this.globalRect.y + current_p.y >= 0) {
          this.globalRect.y += current_p.y;
          y_move = true;
        }
        this.$bus.$emit("globalMove", current_p, x_move, y_move, evt);
        this.globalMoveLastMouse = move_p;
        return;
      }
    },
    getRect() {
      let x = Number.MAX_VALUE,
        y = Number.MAX_VALUE,
        width = Number.MIN_VALUE,
        height = Number.MIN_VALUE;
      for (let i = 0; i < this.nodes.length; i++) {
        let node = this.nodes[i];
        if (node.point.x < x) {
          x = node.point.x;
        }
        if (node.point.x + node.width > width) {
          width = node.point.x + node.width;
        }
        if (node.point.y < y) {
          y = node.point.y;
        }
        if (node.point.y + node.height > height) {
          height = node.point.y + node.height;
        }
      }
      for (let i = 0; i < this.lines.length; i++) {
        let line = this.lines[i];
        for (let n = 0; n < line.path.length; n++) {
          if (line.path[n].x < x) {
            x = line.path[n].x;
          }
          if (line.path[n].x > width) {
            width = line.path[n].x;
          }
          if (line.path[n].y < y) {
            y = line.path[n].y;
          }
          if (line.path[n].y > height) {
            height = line.path[n].y;
          }
        }
      }
      return {
        x: x,
        y: y,
        width: width,
        height: height,
      };
    },
    getWheelDirect(e) {
      e = e || window.event;
      let direction;
      if (e.wheelDelta) {
        //判断浏览器IE，谷歌滑轮事件
        if (e.wheelDelta > 0) {
          //当滑轮向上滚动时
          direction = "up";
        }
        if (e.wheelDelta < 0) {
          //当滑轮向下滚动时
          direction = "down";
        }
      } else if (e.detail) {
        //Firefox滑轮事件
        if (e.detail > 0) {
          //当滑轮向上滚动时
          direction = "up";
        }
        if (e.detail < 0) {
          //当滑轮向下滚动时
          direction = "down";
        }
      }
      return direction;
    },
    scrollwheel(e) {
      e = e || window.event;
      // if(e.preventDefault && e.ctrlKey) e.preventDefault();
      // if(e.ctrlKey) e.returnValue = false;
      if (e.wheelDelta && e.ctrlKey) {
        e.preventDefault();
        const direction = this.getWheelDirect(e);
        this.rootScale(direction);
      }
    },
    rootScale(direction) {
      let scale = 1;
      if (direction === "up") {
        scale = Number(
          (this.scale + 0.05 < 2 ? this.scale + 0.05 : 2).toFixed(2)
        );
      } else {
        scale = Number((this.scale > 0.4 ? this.scale - 0.05 : 0.4).toFixed(2));
      }
      this.$store.commit("flow/setPage", {
        scale: scale,
      });
      this.$store.commit("flow/clearPoints");
    },
    selectGlobalMove(e) {
      if (!this.globalSelect) {
        this.globalSelect = document.createElement("div");
        this.globalSelect.className = "select-nodes";
        Object.assign(this.globalSelect.style, {
          zIndex: 9999,
          transform: `translate3d(${this.selectGlobalPos.x / this.scale}px, ${
            this.selectGlobalPos.y / this.scale
          }px, 0)`,
          pointerEvents: "none",
        });
        this.$refs.root.appendChild(this.globalSelect);
      }
      if (this.selectGlobalPos) {
        const start = this.selectGlobalPos;
        const offset = this.$store.getters["flow/getSvgOffset"];
        const mp = this.$store.getters["flow/getMousePointByCanvas"]({
          x: e.pageX,
          y: e.pageY,
        });
        // 基准点是flow div
        const end = {
          x: mp.x + offset.left,
          y: mp.y + offset.top,
        };
        let top, left, width, height;
        if (end.x > start.x) {
          left = start.x;
          width = end.x - start.x;
        } else {
          left = end.x;
          width = start.x - end.x;
        }
        if (end.y > start.y) {
          top = start.y;
          height = end.y - start.y;
        } else {
          top = end.y;
          height = start.y - end.y;
        }
        this.selectGlobalPonits.start.x = left / this.scale;
        this.selectGlobalPonits.start.y = top / this.scale;
        this.selectGlobalPonits.end.x = (left + width) / this.scale;
        this.selectGlobalPonits.end.y = (top + height) / this.scale;
        Object.assign(this.globalSelect.style, {
          transform: `translate3d(${left / this.scale}px, ${
            top / this.scale
          }px, 0)`,
          width: this.toPx(width / this.scale),
          height: this.toPx(height / this.scale),
        });
      }
    },
    selectGlobalEnd(e) {
      this.isGlobalSelect = false;
      if (this.globalSelect) {
        const end = this.$store.getters["flow/getMousePointByCanvas"]({
          x: e.pageX,
          y: e.pageY,
        });
        this.$bus.$emit(
          "selectGlobal",
          this.selectGlobalPonits.start,
          this.selectGlobalPonits.end
        );
        this.$refs.root.removeChild(this.globalSelect);
        this.globalSelect = null;
      }
    },
    documentCopy(e) {
      const selectedList = this.selectedList;
      if (selectedList && selectedList.length > 1) {
        this.ctrlCopyNodes = [...selectedList];
      } else {
        this.ctrlCopyNodes = [this.checked];
      }
    },
    pasteNode(e) {
      if (!this.ctrlCopyNodes) {
        return false;
      }
      let copyNodes = [];
      const { fromNodeIds, toNodeIds, newLines } = this.getBeforeCopyLine(
        this.ctrlCopyNodes
      );
      this.ctrlCopyNodes.forEach((item) => {
        const copyNode = item;
        // 复制一个节点
        const fromCopyId = copyNode.id;
        const id = tools.uuid();
        const newcopyNode = JSON.parse(JSON.stringify(copyNode));
        const point = Object.assign({}, newcopyNode, {
          id: tools.uuid(),
          out: [],
          in: [],
          x: copyNode.point.x + 10,
          y: copyNode.point.y + 10,
        });
        point.attributes.BaseInfo.ID = id;
        const newNode = this.copyNodeNext(point, fromCopyId, copyNode);
        this.resetLineFromAndTo(fromNodeIds, toNodeIds, newNode, copyNode);
        copyNodes.push(newNode);
        this.checked = newNode;
      });
      this.copyEventsLines(newLines);
      this.ctrlCopyNodes = copyNodes;
      this.$store.commit("flow/setSelectedList", copyNodes);
    },
    async toPrintImage(isSave, ids) {
      const imgUrl = await this.html2canvas();
      // 保存
      if (isSave) {
        this.printImageUrl = imgUrl;
      } else {
        // 下载
        var link = document.createElement("a");
        link.download = this.flowInfo.BaseInfo.Name + ".png";
        link.href = imgUrl;
        this.$nextTick(() => {
          link.click();
        });
      }
      setTimeout(() => {
        if (ids) {
          this.hideNodes(ids, false);
        }
        if (isSave) {
          // 保证节点显示后保存
          this.$nextTick(() => {
            this.saveFlow();
          });
        }
        this.isPrintImage = false;
      }, 300);
    },
    html2canvas() {
      const page = this.page;
      const width =
        this.scale > 1 ? this.width * this.scale : this.width / this.scale;
      const height =
        this.scale > 1 ? this.height * this.scale : this.height / this.scale;
      return html2canvas(this.$refs.container, {
        useCORS: true,
        x: page.left,
        y: page.top,
        width: width,
        height: height,
      }).then((canvas) => {
        var imgUrl = canvas.toDataURL("image/png", 1); // 此方法可以设置截图质量（0-1）
        return imgUrl;
      });
    },
    printImage(isSave) {
      if (this.nodes.length === 0) {
        this.toPrintImage(isSave);
        return;
      }
      this.isPrintImage = true;
      let params = {
        nodes: this.nodes,
        value: [],
      };
      dsf.layer.openDialog({
        title: "选择需要隐藏的节点",
        width: "400px",
        height: "266px",
        overflow: "auto",
        content: "SumStartNode",
        params: params,
        btns: [
          {
            text: "确定",
            handler: async (component) => {
              const ids = component.getContent();
              if (ids.length > 0) {
                this.hideNodes(ids, true);
                this.toPrintImage(isSave, ids);
                return;
              }
              this.toPrintImage(isSave);
            },
          },
          {
            text: "取消",
          },
        ],
      });
    },
    hideNodes(ids, isHide) {
      let lines = [];
      ids.forEach((id) => {
        // 隐藏节点、线
        this.nodes.forEach((item) => {
          if (item.id === id) {
            item.isHide = isHide;
            lines = [...item.in, ...item.out];
          }
        });
        // 隐藏边界事件
        this.events.forEach((ev) => {
          if (ev.nodeId === id) {
            ev.isHide = isHide;
            lines = [...lines, ...ev.out];
          }
        });
      });
      if (lines.length > 0) {
        this.$bus.$emit("hideLines", lines, isHide);
      }
    },
    ...FlowFun,
    getProperties
  },
};
</script>
<style lang="scss">
@import "_platform/assets/styles/designer/workFlow.scss";
body {
  overflow: hidden;
}
.ds_page {
  overflow: auto;
  .flow {
    position: relative;
    width: 100%;
    height: 100%;
    // 打印图片 不能有动画
    &.is-print-image {
      .lane,
      .nodeOuter,
      .nodename,
      .tool_event {
        animation: none !important;
      }
      // 打印图片 ellipsis -webkit-box 不显示文字
      .nodename,
      .task-msg {
        text-overflow: clip;
        display: inline-block;
      }
    }

    .root {
      position: relative;
      min-width: 100%;
      min-height: 100%;
    }
  }
}
</style>
