function exportXML() {
  let xmlString = this.getXMLStr();
  var blob = new Blob([xmlString], {
    type: "text/xml;charset=UTF-8",
    encoding: "UTF-8",
  });
  var link = document.createElement("a");
  const url = window.URL.createObjectURL(blob);
  link.href = url;
  link.download = this.flowInfo?.BaseInfo?.Name ?? "流程";
  link.click();
}

function uploadJson(file, fileList) {
  var reader = new FileReader();
  dsf.layer
    .confirm({
      title: "",
      message: "导入后模式会切换为导入文件的模式，确认导入吗？",
    })
    .then(() => {
      reader.onload = (evt) => {
        const jsonFile = JSON.parse(evt.target.result);
        if(!jsonFile?.WorkFlow) {
          dsf.layer.message("导入数据不满足要求", false);
          return;
        }
        this.$store.commit("flow/clearLanes");
        for (let i = 0;i < this.events.length; i++) {
          this.deleteEvent(this.events[i].id, this.events);
          i--;
        }
        for (let i = 0;i < this.lines.length; i++) {
          this.deleteLine(this.lines[i]);
          i--;
        }
        for (let i = 0;i < this.nodes.length; i++) {
          this.deleteNode(this.nodes[i]);
          i--;
        }
        this.flowInfo = jsonFile.WorkFlow;
        this.flowInfo.BaseInfo.ID = this.$route.query.id;
        if (this.$route.query.sName) {
          this.flowInfo.BaseInfo.Name = this.$route.query.sName;
        }
        this.setFlowModel(jsonFile.flowModel || jsonFile.WorkFlow?.flowModel || "normal");
        this.loadElement();
      };
      reader.readAsText(file.raw);
    })
    .catch(() => {});
}

function getXMLStr() {
  let data = this.getSaveData();
  data = _.cloneDeep(data);
  // 兼容老版本数据处理
  const types = {
    201: 0,
    101: 1,
    299: 2,
    302: 3,
    102: 4,
  };
  data.WorkFlow.BaseInfo.Vendor.APP = {
    ID: "",
    Name: this.$route.query.sAPPName ?? "",
    Desc: "",
    ModuleID: this.$route.query.sModuleID ?? "",
  };
  data?.WorkFlow?.NodeList?.forEach((item) => {
    item.BaseInfo.Type = types[item.BaseInfo.Type] ?? item.BaseInfo.Type;
    Object.assign(item, item.BaseInfo);
    if(item.InLine) {
      Object.assign(item.InLine, {
        Require: "",
        IsSpecialSend: "",
        Multiplayer: {
          IsEnabled: "",
          SendMode: "",
          Poior: ""
        },
        ExAction: "",
        SendBackAction: "",
        BanSendBack: ""
      });
    }
    if (item?.InLine?.RangeRule?.Rule) {
      item.InLine.RangeRule.RuleList = item.InLine.RangeRule.Rule;
      // 发送条件数据处理
      item.InLine.RangeRule.RuleList.forEach((item) => {
        item.RangeType = item.Type;
        item.Range = item.name;
        // 直接选人 指定人员
        if (item.Type == 21) {
          item.IsDeep = item.IsDeep ?? "";
          item.IsShowDeptUser = "";
        }
        // 直接选人 继承节点经办人
        if (item.Type == 22) {
          item.NodeRowIndex = "";
          item.desc = "";
          item.IsDeep = item.IsDeep ?? "";
          item.IsShowDeptUser = "";
        }
        // 部门 + 角色 高级模式
        if (item.Type == 23) {
          item.Script = "1";
          item.Sql = item.exp;
          item.SuperType = "6";
        }
        // 部门 + 角色 指定部门
        if (item.Type == 31) {
          item.IsShowDeptUser = "";
        }
        // 部门 + 角色 继承节点部门
        if (item.Type == 32) {
          item.NodeRowIndex = "";
          item.desc = "";
          item.IsDeep = item.IsDeep ?? "";
          item.IsShowDeptUser = "";
          item.Level = 1;
          item.level = 1;
        }
        // 部门 + 角色 高级模式
        if (item.Type == 33) {
          item.IsDeep = item.IsDeep ?? "";
          item.IsShowDeptUser = "";
          item.Script = "1";
          item.Sql = item.exp;
          item.SuperType = "6";
        }
        // 人 + 关系  继承节点经办人
        if (item.Type == 41) {
          item.desc = "";
          item.level = "1";
        }
        // 人 + 关系  继承节点经办人
        if (item.Type == 42) {
          item.Script = "1";
          item.Sql = item.exp;
          item.SuperType = "6";
        }
        delete item.id;
        delete item.IsUser;
        delete item.name;
        delete item.Type;
      });
      delete item.InLine.RangeRule.Rule;
    }
    if(item.OutLine) {
      Object.assign(item.OutLine, {
        SendConditions: "",
        PreAction: "",
      });
    }
    delete item.BaseInfo;
  });
  data?.WorkFlow?.LineList?.forEach((item) => {
    Object.assign(item, {
      Alias: "",
      Desc: "",
      SendParameter: {
        IsAutoSend: "",
        ShowOrder: "",
        IsInheritDept: "",
        IsBackTrack: "",
        Multiplayer: {
          SendMode: "",
        },
        ExAction: "",
        SendBackAction: "",
        IsRemind: "",
        RemindContent: "",
      }
    }, item.BaseInfo);
    delete item.BaseInfo;
  });
  data.WorkFlow.Corridor = {
    CorridorList: data.WorkFlow.LaneList.horizontal,
    StageList: data.WorkFlow.LaneList.vertical,
  };
  delete data.WorkFlow.LaneList;
  // console.log(data);
  let WorkFlow = this.dataToXML(data);
  return WorkFlow;
}

function dataToXML(data, key) {
  let WorkFlow = "";
  if (_.isArray(data)) {
    // console.log(key);
    const isList = key?.indexOf("List") > -1;
    const realKey = key.replace("List", "");
    data.forEach((item, index) => {
      if (isList) {
        WorkFlow += `<${realKey}>${this.dataToXML(item)}</${realKey}>`;
      } else {
        WorkFlow += `${this.dataToXML(item)}`;
        if (index < data.length - 1) {
          WorkFlow += `</${realKey}>`;
        }
        if (index < data.length - 1) {
          WorkFlow += `<${realKey}>`;
        }
      }
    });
  } else if (_.isObject(data)) {
    Object.keys(data).forEach((item) => {
      if(item !== 'sImgData') {
        WorkFlow += `<${item}>${this.dataToXML(data[item], item)}</${item}>`;
      }
    });
  } else {
    return data;
  }
  return WorkFlow;
}

function exportFlowJson() {
  const paramID = this.sID;
  const zipFileName = this.flowInfo.BaseInfo.Name;
  let url = dsf.url.getWebPath("wfk/exportJsonWF");
  $(
    "<form action=" +
      url +
      ' method="get">' + // action请求路径及推送方法
      '<input type="text" name="sID" value="' +
      paramID +
      '"/>' + // 文件id
      '<input type="text" name="zipName" value="' +
      zipFileName +
      '"/>' + // 文件名称
      "</form>"
  )
    .appendTo("body")
    .submit()
    .remove();
}

function alignLeft(selectedList, left) {
  selectedList.forEach((node) => {
    node.point.x = left;
    this.$bus.$emit("nodeMove", node);
  });
}

function alignRight(selectedList, right) {
  selectedList.forEach((node) => {
    node.point.x = right - node.width;
    this.$bus.$emit("nodeMove", node);
  });
}

function alignTop(selectedList, top) {
  selectedList.forEach((node) => {
    node.point.y = top;
    this.$bus.$emit("nodeMove", node);
  });
}

function alignBottom(selectedList, bottom) {
  selectedList.forEach((node) => {
    node.point.y = bottom - node.height;
    this.$bus.$emit("nodeMove", node);
  });
}

function alignMiddle(selectedList, middle) {
  selectedList.forEach((node) => {
    node.point.y = middle - node.height / 2;
    this.$bus.$emit("nodeMove", node);
  });
}

function alignCenter(selectedList, center) {
  selectedList.forEach((node) => {
    node.point.x = center - node.width / 2;
    this.$bus.$emit("nodeMove", node);
  });
}

function alignHorizontal(selectedList) {
  // 先排序
  const sortX = selectedList.sort((a, b) => a.point.x - b.point.x);
  // 存放间距
  let spaces = [];
  sortX.reduce((before, after) => {
    const space = after.point.x - (before.point.x + before.width);
    spaces.push(space);
    return after;
  });
  // 计算平均间距
  const between = spaces.reduce((a, b) => a + b) / spaces.length;
  sortX.forEach((node, index) => {
    if (index !== 0 && index !== sortX.length - 1) {
      node.point.x =
        sortX[index - 1].point.x + sortX[index - 1].width + between;
      this.$bus.$emit("nodeMove", node);
    }
  });
}

function alignVertical(selectedList) {
  // 先排序
  const sortY = selectedList.sort((a, b) => a.point.y - b.point.y);
  // 存放间距
  let spaces = [];
  sortY.reduce((before, after) => {
    const space = after.point.y - (before.point.y + before.height);
    spaces.push(space);
    return after;
  });
  // 计算平均间距
  const between = spaces.reduce((a, b) => a + b) / spaces.length;
  sortY.forEach((node, index) => {
    if (index !== 0 && index !== sortY.length - 1) {
      node.point.y =
        sortY[index - 1].point.y + sortY[index - 1].height + between;
      this.$bus.$emit("nodeMove", node);
    }
  });
}

function alignment(dir) {
  const selectedList = this.$store.state.flow.selectedList;
  let top = 0;
  let right = 0;
  let bottom = 0;
  let left = 0;
  let middle = 0;
  let center = 0;
  selectedList.forEach((node, index) => {
    const {
      width,
      height,
      point: { x, y },
    } = node;
    if (index === 0) {
      top = y;
      left = x;
      right = x + width;
      bottom = y + height;
    }
    if (left > x) {
      left = x;
    }
    if (top > y) {
      top = y;
    }
    if (right < x + width) {
      right = x + width;
    }
    if (bottom < y + height) {
      bottom = y + height;
    }
  });
  middle = (top + bottom) / 2;
  center = (left + right) / 2;
  switch (dir) {
  case "left":
    this.alignLeft(selectedList, left);
    break;
  case "top":
    this.alignTop(selectedList, top);
    break;
  case "right":
    this.alignRight(selectedList, right);
    break;
  case "bottom":
    this.alignBottom(selectedList, bottom);
    break;
  case "middle":
    this.alignMiddle(selectedList, middle);
    break;
  case "center":
    this.alignCenter(selectedList, center);
    break;
  case "horizontal":
    this.alignHorizontal(selectedList);
    break;
  case "vertical":
    this.alignVertical(selectedList);
    break;
  }
}

export default {
  exportXML,
  uploadJson,
  getXMLStr,
  dataToXML,
  exportFlowJson,
  alignment,
  alignLeft,
  alignRight,
  alignTop,
  alignBottom,
  alignMiddle,
  alignCenter,
  alignHorizontal,
  alignVertical,
};
