<template>
  <div class="ds_attr_root">
    <div v-show="status === 'base'" class="base">
      <p class="title">
        <span>常用</span>
        <el-button class="el-button-all" size="mini" round @click="status = 'advanced'">
          <span>全部</span>
          <i class="el-icon-arrow-right"></i>
        </el-button>
      </p>
      <div class="el-collapse-item__content">
        <el-form v-for="(item, itemIndex) in data" :key="itemIndex" class="colla-content" label-position="top" label-width="80px">
          <div v-for="(attr, index) in item.attributes" :key="index" class="property-item">
            <el-form-item v-if="!attr.hide && attr.isBase">
              <span slot="label" class="property-item-name">
                {{ attr.name }}
                <DsfIcon v-if="attr.desc" style="margin-left: 5px; cursor: pointer" name="bangzhuzhongxin" @click="showHelp(attr)"></DsfIcon>
                <el-dialog :visible.sync="attr.showHeplerMark" :title="attr.name" :append-to-body="true" :width="'500px'">
                  <div style="max-height: 500px; min-height: 300px; overflow: auto">
                    <span style="display: inline-block; line-height: 1.5em" v-html="attr.desc"></span>
                  </div>
                </el-dialog>
              </span>
              <div v-if="attr.nodeType === 'flow' && attr.nodeName2 === 'Version'" class="inline">
                <el-input-number
                  v-model="attr.value0"
                  controls-position="right"
                  :disabled="subIsDisabled"
                  :min="minValue(attr, 0)"
                  @input="changeVesion(attr, 0)"
                ></el-input-number>
                <span>.</span>
                <el-input-number
                  v-model="attr.value1"
                  controls-position="right"
                  :disabled="subIsDisabled"
                  :min="minValue(attr, 1)"
                  @input="changeVesion(attr, 1)"
                ></el-input-number>
                <span>.</span>
                <el-input v-model="attr.value2" disabled="disabled"></el-input>
              </div>
              <div v-if="attr.unit" class="inline">
                <el-input-number
                  v-model="attr.value"
                  controls-position="right"
                  :disabled="subIsDisabled"
                  :min="1"
                  :max="attr.max"
                  @input="changeProperty(attr)"
                ></el-input-number>
                <span class="unit">{{ attr.unit }}</span>
              </div>
              <el-input v-if="show(attr)" v-model="attr.value" :disabled="subIsDisabled || !attr.editor" @input="changeProperty(attr)"></el-input>
              <el-input-number
                v-if="attr.editor === 'numberbox'"
                v-model="attr.value"
                controls-position="right"
                :disabled="subIsDisabled"
                :min="0"
                @input="changeProperty(attr)"
              ></el-input-number>
              <el-input
                v-if="attr.editor === 'textarea'"
                v-model="attr.value"
                :disabled="subIsDisabled"
                type="textarea"
                placeholder="请输入内容"
                @input="changeProperty(attr)"
              ></el-input>
              <el-select
                v-if="attr.editor && attr.editor.type === 'combobox'"
                v-model="attr.value"
                :name="`${attr.nodeName1 + '_' + attr.nodeName2}`"
                :disabled="subIsDisabled"
                @change="changeProperty(attr)"
              >
                <el-option v-for="(option, i) in attr.editor.options.data" :key="i" :label="option.text" :value="option.value"></el-option>
              </el-select>
              <!--单选-->
              <el-radio-group
                v-if="attr.editor && attr.editor.type === 'radio'"
                v-model="attr.value"
                :disabled="subIsDisabled"
                @change="changeProperty(attr)"
              >
                <el-radio v-for="(option, $index) in attr.editor.options.data" :key="$index" :label="option.value">
                  {{ option.text }}
                </el-radio>
              </el-radio-group>
              <!--多选-->
              <el-checkbox-group
                v-if="attr.editor && attr.editor.type === 'checkbox'"
                v-model="attr.value"
                :disabled="subIsDisabled"
                @change="changeProperty(attr)"
              >
                <el-checkbox v-for="(option, $index) in attr.editor.options.data" :key="$index" :label="option.value">
                  {{ option.text }}
                </el-checkbox>
              </el-checkbox-group>
              <!--弹出框-->
              <DsfButton v-if="attr.type === 'dialog'" type="plain" icon="shezhi" :class="[attr.class]" @click="doOpenDialog(attr)">
                {{ subIsDisabled ? "查看" : "设置" }}
              </DsfButton>
            </el-form-item>
          </div>
        </el-form>
      </div>
    </div>
    <div v-show="status === 'advanced'" class="advanced">
      <p class="title">
        <span>全部</span>
        <el-button class="el-button-base" size="mini" round @click="status = 'base'">
          <i class="el-icon-arrow-left"></i>
          <span>常用</span>
        </el-button>
      </p>
      <el-collapse v-model="activeNames" accordion>
        <el-collapse-item v-for="item in data" :key="item.name" :title="item.name" :name="item.name">
          <el-form label-position="top" label-width="80px" class="colla-content">
            <div v-for="(attr, index) in item.attributes" :key="index" class="property-item">
              <el-form-item v-if="!attr.hide">
                <span slot="label" class="property-item-name">
                  {{ attr.name }}
                  <!-- <el-popover placement="bottom" width="210" trigger="click" :content="attr.desc">
                    <i slot="reference" class="el-icon-question"></i>
                  </el-popover>-->
                  <DsfIcon v-if="attr.desc" style="margin-left: 5px; cursor: pointer" name="bangzhuzhongxin" @click="showHelp(attr)"></DsfIcon>
                  <el-dialog :visible.sync="attr.showHeplerMark" :title="attr.name" :append-to-body="true" :width="'500px'">
                    <div style="max-height: 500px; min-height: 300px; overflow: auto">
                      <span style="display: inline-block; line-height: 1.5em" v-html="attr.desc"></span>
                    </div>
                  </el-dialog>
                </span>
                <div v-if="attr.nodeType === 'flow' && attr.nodeName2 === 'Version'" class="inline">
                  <el-input-number
                    v-model="attr.value0"
                    controls-position="right"
                    :disabled="subIsDisabled"
                    :min="minValue(attr, 0)"
                    @input="changeVesion(attr, 0)"
                  ></el-input-number>
                  <span>.</span>
                  <el-input-number
                    v-model="attr.value1"
                    controls-position="right"
                    :disabled="subIsDisabled"
                    :min="minValue(attr, 1)"
                    @input="changeVesion(attr, 1)"
                  ></el-input-number>
                  <span>.</span>
                  <el-input v-model="attr.value2" disabled="disabled"></el-input>
                </div>
                <div v-if="attr.unit" class="inline">
                  <el-input-number
                    v-model="attr.value"
                    controls-position="right"
                    :disabled="subIsDisabled"
                    :min="0"
                    :max="attr.max"
                    @input="changeProperty(attr)"
                  ></el-input-number>
                  <span class="unit">{{ attr.unit }}</span>
                </div>
                <el-input v-if="show(attr)" v-model="attr.value" :disabled="subIsDisabled || !attr.editor" @input="changeProperty(attr)"></el-input>
                <el-input-number
                  v-if="attr.editor === 'numberbox'"
                  v-model="attr.value"
                  controls-position="right"
                  :disabled="subIsDisabled"
                  :min="0"
                  @input="changeProperty(attr)"
                ></el-input-number>
                <el-input
                  v-if="attr.editor === 'textarea'"
                  v-model="attr.value"
                  :disabled="subIsDisabled"
                  type="textarea"
                  placeholder="请输入内容"
                  @input="changeProperty(attr)"
                ></el-input>
                <el-select
                  v-if="attr.editor && attr.editor.type === 'combobox'"
                  v-model="attr.value"
                  :name="`${attr.nodeName1 + '_' + attr.nodeName2}`"
                  :disabled="subIsDisabled"
                  @change="changeProperty(attr)"
                >
                  <el-option v-for="(option, i) in attr.editor.options.data" :key="i" :label="option.text" :value="option.value"></el-option>
                </el-select>
                <!--单选-->
                <el-radio-group
                  v-if="attr.editor && attr.editor.type === 'radio'"
                  v-model="attr.value"
                  :disabled="subIsDisabled"
                  @change="changeProperty(attr)"
                >
                  <el-radio v-for="(option, $index) in attr.editor.options.data" :key="$index" :label="option.value">
                    {{ option.text }}
                  </el-radio>
                </el-radio-group>
                <!--多选-->
                <el-checkbox-group
                  v-if="attr.editor && attr.editor.type === 'checkbox'"
                  v-model="attr.value"
                  :disabled="subIsDisabled"
                  @change="changeProperty(attr)"
                >
                  <el-checkbox v-for="(option, $index) in attr.editor.options.data" :key="$index" :label="option.value">
                    {{ option.text }}
                  </el-checkbox>
                </el-checkbox-group>
                <!--弹出框-->
                <DsfButton v-if="attr.type === 'dialog'" type="plain" icon="shezhi" :class="attr.class" @click="doOpenDialog(attr)">
                  {{ subIsDisabled ? "查看" : "设置" }}
                </DsfButton>
              </el-form-item>
            </div>
          </el-form>
        </el-collapse-item>
      </el-collapse>
    </div>
  </div>
</template>
<script>
import { GetInlineTypeData, GetIsSelected } from "./propertySource";
import Line from "./Line";
import Lane from "./Lane";
import Node from "./Node";
import BoundaryEvent from "./BoundaryEvent";
import { getObjById, verifySub, getProperties } from "./common";
import { systematicNodeSize } from './tools';
import DsfDesignerDialog from "_platform/designer/propertyEditor/dsfDesignerDialog";

export default {
  name: "FlowInfo",
  components: {
    DsfDesignerDialog,
  },
  props: {
    checked: {
      type: Object,
      default() {
        return {};
      },
    },
    flowInfo: {
      type: Object,
      default() {
        return {};
      },
    },
    nodes: {
      type: Array,
      default() {
        return [];
      },
    },
    lines: {
      type: Array,
      default() {
        return [];
      },
    },
  },
  data() {
    return {
      activeNames: ["1"],
      curSelectObj: null,
      curNodeAttr: null,
      curElement: null,
      AppInfo: null,
      data: [],
      status: "base",
      showHeplerMark: false,
      subIsDisabled: false,
    };
  },
  computed: {
    minValue() {
      return (attr, index) => {
        if (index === 0) {
          return Number(attr.oldValue.split(".")[0]);
        }
        if (attr.value0 > Number(attr.oldValue.split(".")[0]) && index === 1) {
          return 0;
        }
        return Number(attr.oldValue.split(".")[1]);
      };
    },
  },
  watch: {
    checked(ui) {
      // this.loadPropertyWindow(ui);
      // this.subIsDisabled = this.disabledSub();
    },
    "checked.attributes"(val) {
      this.curNodeAttr = val;
      if (val) {
        this.loadPropertyWindow(this.checked);
        this.subIsDisabled = this.disabledSub();
      }
    }
  },
  mounted() {
    this.$bus.$on("updateNodeName", (ui, bool) => {
      if (!bool) {
        this.loadPropertyWindow(ui);
      }
    });
  },
  methods: {
    disabledSub() {
      if (this.curSelectObj && this.curSelectObj.parentNodeId) {
        const parentNode = getObjById(this.nodes, this.curSelectObj.parentNodeId);
        return verifySub(parentNode);
      }
      return false;
    },
    show(attr) {
      return (!attr.nodeType && attr.nodeName2 !== "Version" && !attr.editor && attr.type !== "dialog") || (!attr.unit && attr.editor === "text");
    },
    async loadPropertyWindow(obj) {
      if (Object.keys(obj).length === 0) {
        return;
      }
      const properties = obj ? await this.setPropertyData(obj) : [];
      this.curSelectObj = obj || null;
      // this.curNodeAttr = !obj ? null : obj.attributes;
      this.loadPropertyDom(properties);
    },
    showHelp(attr) {
      this.$set(attr, "showHeplerMark", true);
    },
    async setPropertyData(obj) {
      let pro = null;
      let jsonFile;
      if (obj instanceof Node) {
        jsonFile = 'node' + obj.type;
      } else if (obj instanceof Line) {
        jsonFile = 'line';
      } else if (obj instanceof Lane) {
        jsonFile = 'lane';
      } else if (obj instanceof BoundaryEvent) {
        jsonFile = 'boundaryevent';
      } else {
        // 基本流
        jsonFile = 'flow';
      }
      pro = await getProperties(jsonFile);
      var attr = obj.attributes;
      // 转成平台要的样子
      var group = null;
      var groupIndex = -1;
      let json = [];
      for (let i = 0; i < pro.length; i++) {
        if (pro[i].nodeLevel == 1) {
          // 类似文本框
          pro[i].value = attr[pro[i].nodeName];
        } else if (pro[i].nodeLevel == 2) {
          // 期限类型-会根据流程信息去取值
          if (attr[pro[i].nodeName1]) {
            pro[i].value = attr[pro[i].nodeName1][pro[i].nodeName2];
          } else {
            pro[i].value = "";
          }
        } else if (pro[i].nodeLevel == 3) {
          // 应用名称应用信息 - 初步认为是只读
          if (attr[pro[i].nodeName1] && attr[pro[i].nodeName1][pro[i].nodeName2]) {
            pro[i].value = attr[pro[i].nodeName1][pro[i].nodeName2][pro[i].nodeName3];
          } else {
            pro[i].value = "";
          }
        }
        if (pro[i].group != group) {
          groupIndex++;
          group = pro[i].group;
          json[groupIndex] = {
            name: group,
            attributes: [],
          };
        }
        json[groupIndex]["attributes"].push(pro[i]);
      }
      return json;
    },
    loadPropertyDom(data) {
      let rows = [];
      data.forEach((item) => {
        item.attributes.forEach((attr) => {
          if (attr.editor && attr.editor.type === "checkbox") {
            attr.value = attr.value || [];
          } else if (attr.editor && attr.editor.type !== "checkbox") {
            let value = Number(attr.value);
            value = Number.isNaN(value) ? attr.value : value;
            attr.value = attr.editor?.options && attr?.value ? value : attr.value;
          } else if (attr.nodeType === "flow" && attr.nodeName2 === "Version") {
            // 工作流版本
            attr.oldValue = attr.value;
            attr.value0 = attr.value.split(".")[0];
            attr.value1 = attr.value.split(".")[1];
            attr.value2 = attr.value.split(".")[2];
          } else {
            attr.value = attr.value;
          }

          if (attr.showRob) {
            rows.push(attr);
          }
        });
      });
      this.data = data;

      // 加载处理模式 抢办人数/比例  连线汇总信息
      if (rows && rows.length > 0) {
        rows.forEach((row) => {
          this.showRob(row);
        });
      }
      this.activeNames = [this.data[0].name];
    },
    changeVesion(attr, index) {
      if (attr) {
        if (index === 0) {
          attr.value1 = 0;
          attr.value2 = "0001";
        }
        attr.value = attr.value0 + "." + attr.value1 + "." + attr.value2;
        this.changeProperty(attr);
      }
    },
    changeProperty(rowData) {
      if (rowData.nodeLevel == 1 && rowData.nodeName === "Name") {
        this.curSelectObj.name = rowData.value;
      } else if (rowData.nodeName2 && rowData.nodeName2 == "Name") {
        if (this.curSelectObj instanceof Node) {
          this.curSelectObj.name = rowData.value;
          // this.$bus.$emit("updateName", this.curSelectObj);
          this.$bus.$emit(`updateNodeName${this.isSub ? this.parentNode.id : ""}`, this.curSelectObj, true);
        }
        if (this.curSelectObj instanceof Lane) {
          this.curSelectObj.name = rowData.value;
        }
        if (this.curSelectObj instanceof Line) {
          if(!this.curSelectObj?.edit?.name1) {
            this.curSelectObj.name = rowData.value;
          }
        }
      }
      if (rowData.showRob && rowData.showRob == "GatherRule") {
        // 线 连线类型变化
        if (this.curSelectObj instanceof Line) {
          this.curSelectObj.type = rowData.value;
        }
      }
      if (rowData.nodeName1 === "InLine" && rowData.nodeName2 === "Type") {
        let name = GetInlineTypeData(rowData.value);
        let RangeRule = [];
        const Rule = this.curNodeAttr.InLine.RangeRule && this.curNodeAttr.InLine.RangeRule.Rule;
        if (Rule && Rule.length > 0) {
          Rule.forEach((item, index) => {
            RangeRule.push(item.name + "/" + GetIsSelected(item.IsSelected));
          });
          this.curSelectObj.taskMsg = `${name}: ${RangeRule.join(",")}`;
        }
      }
      if (
        rowData.nodeName1 === "InLine" &&
        rowData.nodeName2 === "RangeRule" &&
        rowData.value &&
        rowData.value.Rule &&
        rowData.value.Rule.length > 0
      ) {
        let name = GetInlineTypeData(this.curNodeAttr.InLine.Type);
        let RangeRule = [];
        rowData.value.Rule.forEach((item, index) => {
          RangeRule.push(item.name + "/" + GetIsSelected(item.IsSelected));
        });

        this.curSelectObj.taskMsg = `${name}: ${RangeRule.join(",")}`;
      }
      // 处理模式为抢办 需要单独处理
      this.showRob(rowData);
      if (rowData.nodeLevel == 1) {
        this.curNodeAttr[rowData.nodeName] = rowData.value;
      } else if (rowData.nodeLevel == 2) {
        this.curNodeAttr[rowData.nodeName1][rowData.nodeName2] = rowData.value;
      } else if (rowData.nodeLevel == 3) {
        this.curNodeAttr[rowData.nodeName1][rowData.nodeName2][rowData.nodeName3] = rowData.value;
      }
      // 线性切换
      if (this.curSelectObj instanceof Line && rowData.nodeName2 === "LineShape") {
        this.$bus.$emit(`changeLineShape${this.isSub ? this.parentNode.id : ""}`, this.curSelectObj);
      }
      // 连线方式
      if (this.curSelectObj instanceof Line && rowData.nodeName2 === "strokeDasharray") {
        this.curSelectObj.strokeDasharray = rowData.value;
        this.curSelectObj.refresh();
      }
    },
    showRob(rowData) {
      if (rowData.showRob) {
        this.data.forEach((item) => {
          item.attributes.forEach((attr) => {
            const showRob = rowData.showRob.split(",");
            showRob.forEach((field) => {
              if (attr.nodeName2 === field || attr.nodeName3 === field) {
                if (field === "RushInfo") {
                  if (rowData.value === 5) {
                    //修改默认值
                    Object.assign(attr, {
                      name: "人数",
                      value: 1,
                      max: 999999,
                      unit: "（人）",
                      hide: false,
                    });
                  } else if (rowData.value === 6) {
                    //修改默认值
                    Object.assign(attr, {
                      name: "比例",
                      value: 60,
                      max: 100,
                      unit: "%",
                      hide: false,
                    });
                  } else {
                    attr.hide = true;
                  }
                } else if (field === "GatherRule") {
                  if (rowData.value === 1 || rowData.value === 2) {
                    attr.hide = false;
                  } else {
                    attr.hide = true;
                  }
                } else if (field === "Params") {
                  if (rowData.value === 0 || rowData.value === 1) {
                    attr.hide = false;
                  } else {
                    attr.hide = true;
                  }
                } else if (field === "Content") {
                  if (rowData.value === 2) {
                    attr.hide = false;
                  } else {
                    attr.hide = true;
                  }
                } else if (field === "Value") {
                  if (rowData.value !== 0) {
                    attr.hide = false;
                  } else {
                    attr.hide = true;
                  }
                } else if (field === "IsEnbled") {
                  if (rowData.value && this.curSelectObj.attributes.BaseInfo && this.curSelectObj.attributes.BaseInfo.Mode === 0) {
                    attr.hide = false;
                  } else {
                    attr.hide = true;
                  }
                } else if (field === "OpinionNodeID") {
                  const BaseInfo = this.curSelectObj.attributes.BaseInfo;
                  setTimeout(() => {
                    if (BaseInfo.Mode === 0 && BaseInfo.SubWFID && BaseInfo.Merge.IsEnbled === 1) {
                      attr.hide = false;
                    } else {
                      attr.hide = true;
                    }
                  }, 0);
                }
              }
            });
          });
        });
      }
    },
    doOpenDialog(rowData) {
      let data = rowData;
      let params = {
        code: (data.value && data.value.Expression) || "",
        language: "java",
        checked: this.checked,
        value: data.value,
      };
      if (data.config && data.config.content) {
        if (data.config.content === "RangeRule" || data.config.content === "SumAllNode") {
          params.nodes = this.nodes;
          params.value = data.value || {};
        } else if (data.config.content === "Summary") {
          params.lines = this.lines;
          params.value = data.value || {};
        } else if (data.config.content === "SendCondition") {
          params.lines = this.lines;
          params.value = data.value || {};
        } else if (data.config.content === "EventSetting") {
          params.isEvent = true;
        } else if (data.config.content === "SetSubFlow") {
          params.mode = this.curSelectObj.attributes.BaseInfo.Mode;
        } else if (data.config.content === "SetOpinionNode") {
          params.subNodes = this.curSelectObj.nodes;
        } else if (data.config.content === "SumStartNode") {
          params.nodes = this.nodes;
          params.value = data.value || [];
        }
      }
      dsf.layer.openDialog({
        title: data.name,
        width: data.config.width,
        height: data.config.height,
        overflow: "auto",
        content: data.config.content,
        params: params,
        btns: [
          {
            class: this.subIsDisabled ? "hide" : "",
            text: "确定",
            handler: (component) => {
              // 选择子流程 子流程单独处理
              if (data.config.content === "SetSubFlow") {
                if (component.validate()) {
                  const checked = component.yes();
                  data.value = checked.SubWFID;
                  this.curSelectObj.attributes.BaseInfo.Mode = checked.Mode;
                  this.curSelectObj.attributes.BaseInfo.SubWFName = checked.SubWFName;
                  this.curSelectObj.attributes.BaseInfo.OpinionNodeID = null;
                  this.curSelectObj.attributes.BaseInfo.OpinionNodeName = null;
                  if (!checked.SubWFID) {
                    this.$bus.$emit("initSubNode", this.curSelectObj, []);
                    return true;
                  }
                  return this.$http
                    .get("wfm/getWFDefinition", {
                      sID: checked.SubWFID,
                    })
                    .then((res) => {
                      this.changeProperty(data);
                      this.$bus.$emit("initSubNode", this.curSelectObj, (res.data.data.WorkFlow && res.data.data.WorkFlow.ElementList) || []);
                      return true;
                    })
                    .catch((error) => {});
                }
                return false;
              } else if (data.config.content === "SetOpinionNode") {
                const checked = component.yes();
                data.value = checked.id;
                this.curSelectObj.attributes.BaseInfo.OpinionNodeName = checked.name;
                this.changeProperty(data);
              } else if (data.config.content === "SelectImg") {
                data.value = component.getContent();
                Object.assign(this.curSelectObj, systematicNodeSize[data.value]);
                this.curSelectObj.imgType = data.value;
                console.log(this.curSelectObj);
                this.$bus.$emit("nodeMove", this.curSelectObj);
                this.changeProperty(data);
              } else {
                data.value = component.getContent();
                this.changeProperty(data);
              }
            },
          },
          {
            text: "取消",
          },
        ],
      });
      if (data.config.content === "SetSubFlow" && !data.value) {
        dsf.layer.message("请慎重操作，选择子流程任务后，将自动获取子流程任务，覆盖子流程");
      }
    },
  },
};
</script>
<style lang="scss" scoped>
.inline {
  display: flex;
  /deep/ .el-input__inner {
    text-align: center;
    padding: 0;
  }
  .el-input-number {
    width: 300px;
