<template>
  <div class="lanes">
    <div
      v-for="(lane, index) in lanes"
      :key="lane.id || index"
      :index="index"
      :style="{
        left: toPx(lane.left),
        top: toPx(lane.top),
        width: lane.direction === 'vertical' ? toPx(lane.size) : '',
        height: lane.direction === 'horizontal' ? toPx(lane.size) : '',
      }"
      :class="{ selected: checked === lane }"
      class="lane"
      @click="selectLane($event, lane)"
    >
      <div class="head">
        <div class="title">
          <el-popover
            v-model="lane.isEdit"
            :placement="lane.direction === 'vertical' ? 'bottom' : 'right'"
            title="修改名称"
            width="200"
            trigger="manual"
          >
            <div class="el-input">
              <input
                v-if="lane.isEdit"
                v-model="lane.name"
                v-focus
                autofocus
                class="el-input__inner"
                type="text"
                placeholder="请输入内容"
                @change="
                  lane.attributes.Name = lane.name;
                  hideEdit();
                "
              />
            </div>
            <span
              slot="reference"
              class="textContent ellipsis"
              :style="{
                width: toPx(lane.size),
                transform: lane.direction === 'horizontal' ? `rotate(-90deg) translateX(-${lane.size}px)` : '',
              }"
              @dblclick="edit(lane)"
            >
              {{ lane.name }}
            </span>
          </el-popover>
          <img class="plus left" :src="require('_platform/assets/styles/themes/workFlow/addLane.png')" @click="addLane(lane.direction, index)" />
          <img class="plus right" :src="require('_platform/assets/styles/themes/workFlow/addLane.png')" @click="addLane(lane.direction, index + 1)" />
          <span class="resizeLane" @mousedown="dragLane($event, lane)"></span>
        </div>
      </div>
      <div class="bg/"></div>
      <div class="laneSelected"></div>
    </div>
  </div>
</template>
<script>
/* eslint-disable no-unused-vars */
import { LaneDirection } from "./tools";

export default {
  name: "Lanes",
  props: {
    lanes: {
      type: Array,
      default() {
        return [];
      },
    },
    checked: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  data() {
    return {
      current: null,
      minSize: 100, // 最小的泳道宽度/高度
    };
  },
  mounted() {
    document.addEventListener("mousedown", (e) => {
      const target = e.target;
      if (
        target.getAttribute("role") !== "tooltip" &&
        target?.parentElement?.getAttribute("role") !== "tooltip" &&
        target?.parentElement?.parentElement.getAttribute("role") !== "tooltip"
      ) {
        this.hideEdit();
      }
    });
    document.addEventListener("mousemove", (e) => {
      this.moveLane(e);
    });
    document.addEventListener("mouseup", (e) => {
      this.dropLane(e);
    });
  },
  methods: {
    toPx(number) {
      return `${number}px`;
    },
    selectLane(e, lane) {
      this.$bus.$emit("update-checked", lane);
      e.stopPropagation();
    },
    addLane(direction, index) {
      this.$emit("addLane", direction, index);
    },
    dragLane(e, item) {
      this.$bus.$emit("update-checked", item);
      this.current = { ...item };
      this.$bus.$emit("update-baseLine", {
        show: this.current.direction,
        top: 0,
        left: 0,
      });
      // 鼠标点击的位置在整个页面上的坐标是（e.clientX，e.clientY）
      // 鼠标点击的位置在图片的位置坐标为（X1，Y1）
    },
    dropLane(e) {
      if (!this.current) {
        return;
      }
      const page = this.$store.state.flow.page;
      const svgRoot = this.$store.state.flow.svgRoot;
      const scale = this.$store.state.flow.page.scale;
      if (this.current.direction === LaneDirection.vertical) {
        this.current.size = (e.pageX - this.current.left - page.left + svgRoot.scrollLeft) / scale;
      } else {
        this.current.size = (e.pageY - this.current.top - page.top + svgRoot.scrollTop) / scale;
      }
      this.current.size = this.current.size > this.minSize ? this.current.size : this.minSize;
      // 根据鼠标点击的位置在整个页面上的坐标和在图片上的坐标来确定整个图片的坐标
      this.$bus.$emit("update-baseLine", {
        showV: false,
        showH: false,
        top: 0,
        left: 0,
      });
      this.$store.commit("flow/changeLane", this.current);
      this.$bus.$emit("resize");
      this.current = null;
    },
    moveLane(e) {
      if (!this.current) {
        return false;
      }
      let resize;
      const page = this.$store.state.flow.page;
      const svgRoot = this.$store.state.flow.svgRoot;
      const scale = this.$store.state.flow.page.scale;
      if (this.current.direction === LaneDirection.vertical) {
        const minLeft = this.current.left + this.minSize;
        const current = e.pageX - page.left + svgRoot.scrollLeft;
        resize = {
          top: 10,
          left: (current > minLeft * scale ? current : minLeft * scale) / scale,
        };
      } else {
        const minTop = this.current.top + this.minSize;
        const current = e.pageY - page.top + svgRoot.scrollTop;
        resize = {
          top: (current > minTop * scale ? current * scale : minTop) / scale,
          left: 10,
        };
      }
      const baseLine = Object.assign(resize, {
        showV: this.current.direction === "vertical",
        showH: this.current.direction === "horizontal",
      });
      this.$bus.$emit("update-baseLine", baseLine);
      this.$bus.$emit("autoScroll", {
        type: "lane",
        data: this.current,
        mouse: {
          x: e.pageX,
          y: e.pageY,
        },
      });
    },
    edit(lane) {
      this.lanes.forEach((item) => {
        if (lane === item) {
          item.isEdit = true;
        } else {
          item.isEdit = false;
        }
      });
    },
    hideEdit() {
      this.lanes.forEach((item) => {
        item.isEdit = false;
      });
    },
  },
};
</script>
<style lang="scss" scoped>
.lanes {
  position: relative;
  width: 100%;
  height: 100%;
}
</style>
